import L from 'leaflet';
import '../../core/Base';
import SimpleShape from './SimpleShape';
import TextMessage from './TextMessage';
import '../../util/Util';

export let DrawRectangle = SimpleShape.extend({
    statics: {
        TYPE: 'rectangle'
    },

    options: {
        shapeOptions: {
            stroke: true,
            color: '#f06eaa',
            weight: 4,
            opacity: 0.5,
            fill: true,
            fillColor: null, //same as color by default
            fillOpacity: 0.2,
            clickable: true
        },
        metric: true // Whether to use the metric measurement system or imperial
    },

    initialize: function (map, options) {
        // Save the type so super can fire, need to do this as cannot do this.TYPE :(
        this.type = DrawRectangle.TYPE;

        this._initialLabelText = TextMessage.draw.handlers.rectangle.tooltip.start;

        SimpleShape.prototype.initialize.call(this, map, options);
    },

    _drawShape: function (latlng) {
        if (!this._shape) {
            this._shape = new L.Rectangle(new L.LatLngBounds(this._startLatLng, latlng), this.options.shapeOptions);
            this._map.addLayer(this._shape);
        } else {
            this._shape.setBounds(new L.LatLngBounds(this._startLatLng, latlng));
        }
    },

    _fireCreatedEvent: function () {
        var rectangle = new L.Rectangle(this._shape.getBounds(), this.options.shapeOptions);
        SimpleShape.prototype._fireCreatedEvent.call(this, rectangle);
    },

    _getTooltipText: function () {
        var tooltipText = SimpleShape.prototype._getTooltipText.call(this),
            shape = this._shape,
            latLngs, area, subtext;

        if (shape) {
            latLngs = this._shape.getLatLngs();
            area = L.Util.geodesicArea(latLngs);
            subtext = L.Util.readableArea(area, this.options.metric);
        }

        return {
            text: tooltipText.text,
            subtext: subtext
        };
    }
});

export let drawRectangle =  function (map, options) {
    return new DrawRectangle(map, options);
}

L.geoway.control.drawRectangle = drawRectangle;
L.geoway.control.DrawRectangle = DrawRectangle;
L.Draw = L.Draw ? L.Draw : {};
L.Draw.Rectangle = DrawRectangle;
