import PathEditor from './PathEditor';
import {EditableMixin} from "./Editable";
import L from 'leaflet';

// 🍂namespace Editable; 🍂class RectangleEditor
// 🍂inherits PathEditor
let RectangleEditor = PathEditor.extend({

    CLOSED: true,
    MIN_VERTEX: 4,

    options: {
        skipMiddleMarkers: true
    },

    extendBounds: function (e) {
        var index = e.vertex.getIndex(),
            next = e.vertex.getNext(),
            previous = e.vertex.getPrevious(),
            oppositeIndex = (index + 2) % 4,
            opposite = e.vertex.latlngs[oppositeIndex],
            bounds = new L.LatLngBounds(e.latlng, opposite);
        // Update latlngs by hand to preserve order.
        previous.latlng.update([e.latlng.lat, opposite.lng]);
        next.latlng.update([opposite.lat, e.latlng.lng]);
        this.updateBounds(bounds);
        this.refreshVertexMarkers();
    },

    onDrawingMouseDown: function (e) {
        PathEditor.prototype.onDrawingMouseDown.call(this, e);
        this.connect();
        var latlngs = this.getDefaultLatLngs();
        // L.Polygon._convertLatLngs removes last latlng if it equals first point,
        // which is the case here as all latlngs are [0, 0]
        if (latlngs.length === 3) latlngs.push(e.latlng);
        var bounds = new L.LatLngBounds(e.latlng, e.latlng);
        this.updateBounds(bounds);
        this.updateLatLngs(bounds);
        this.refresh();
        this.reset();
        // Stop dragging map.
        // L.Draggable has two workflows:
        // - mousedown => mousemove => mouseup
        // - touchstart => touchmove => touchend
        // Problem: L.Map.Tap does not allow us to listen to touchstart, so we only
        // can deal with mousedown, but then when in a touch device, we are dealing with
        // simulated events (actually simulated by L.Map.Tap), which are no more taken
        // into account by L.Draggable.
        // Ref.: https://github.com/Leaflet/Leaflet.Editable/issues/103
        e.originalEvent._simulated = false;
        this.map.dragging._draggable._onUp(e.originalEvent);
        // Now transfer ongoing drag action to the bottom right corner.
        // Should we refine which corner will handle the drag according to
        // drag direction?
        latlngs[3].__vertex.dragging._draggable._onDown(e.originalEvent);
    },

    onDrawingMouseUp: function (e) {
        this.commitDrawing(e);
        e.originalEvent._simulated = false;
        PathEditor.prototype.onDrawingMouseUp.call(this, e);
    },

    onDrawingMouseMove: function (e) {
        e.originalEvent._simulated = false;
        PathEditor.prototype.onDrawingMouseMove.call(this, e);
    },


    getDefaultLatLngs: function (latlngs) {
        return latlngs || this.feature._latlngs[0];
    },

    updateBounds: function (bounds) {
        this.feature._bounds = bounds;
    },

    updateLatLngs: function (bounds) {
        var latlngs = this.getDefaultLatLngs(),
            newLatlngs = this.feature._boundsToLatLngs(bounds);
        // Keep references.
        for (var i = 0; i < latlngs.length; i++) {
            latlngs[i].update(newLatlngs[i]);
        }
    }

});

var RectangleMixin = {

    getEditorClass: function (tools) {
        return (tools && tools.options.rectangleEditorClass) ? tools.options.rectangleEditorClass : RectangleEditor;
    }

};

if (L.Rectangle) {
    L.Rectangle.include(EditableMixin);
    L.Rectangle.include(RectangleMixin);
}

export default RectangleEditor;
