import BaseEditor from './BaseEditor';
import Editable,{isFlat} from './Editable';

// 🍂namespace Editable; 🍂class PathEditor
// 🍂inherits BaseEditor
// Base class for all path editors.
let PathEditor = BaseEditor.extend({

    CLOSED: false,
    MIN_VERTEX: 2,

    addHooks: function () {
        BaseEditor.prototype.addHooks.call(this);
        if (this.feature) this.initVertexMarkers();
        return this;
    },

    initVertexMarkers: function (latlngs) {
        if (!this.enabled()) return;
        latlngs = latlngs || this.getLatLngs();
        if (isFlat(latlngs)) this.addVertexMarkers(latlngs);
        else for (var i = 0; i < latlngs.length; i++) this.initVertexMarkers(latlngs[i]);
    },

    getLatLngs: function () {
        return this.feature.getLatLngs();
    },

    // 🍂method reset()
    // Rebuild edit elements (Vertex, MiddleMarker, etc.).
    reset: function () {
        this.editLayer.clearLayers();
        this.initVertexMarkers();
    },

    addVertexMarker: function (latlng, latlngs) {
        return new this.tools.options.vertexMarkerClass(latlng, latlngs, this);
    },

    onNewVertex: function (vertex) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:new: VertexEvent
        // Fired when a new vertex is created.
        this.fireAndForward('editable:vertex:new', {latlng: vertex.latlng, vertex: vertex});
    },

    addVertexMarkers: function (latlngs) {
        for (var i = 0; i < latlngs.length; i++) {
            this.addVertexMarker(latlngs[i], latlngs);
        }
    },

    refreshVertexMarkers: function (latlngs) {
        latlngs = latlngs || this.getDefaultLatLngs();
        for (var i = 0; i < latlngs.length; i++) {
            latlngs[i].__vertex.update();
        }
    },

    addMiddleMarker: function (left, right, latlngs) {
        return new this.tools.options.middleMarkerClass(left, right, latlngs, this);
    },

    onVertexMarkerClick: function (e) {
        Editable.makeCancellable(e);
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:click: CancelableVertexEvent
        // Fired when a `click` is issued on a vertex, before any internal action is being processed.
        this.fireAndForward('editable:vertex:click', e);
        if (e._cancelled) return;
        if (this.tools.drawing() && this.tools._drawingEditor !== this) return;
        var index = e.vertex.getIndex(), commit;
        if (e.originalEvent.ctrlKey) {
            this.onVertexMarkerCtrlClick(e);
        } else if (e.originalEvent.altKey) {
            this.onVertexMarkerAltClick(e);
        } else if (e.originalEvent.shiftKey) {
            this.onVertexMarkerShiftClick(e);
        } else if (e.originalEvent.metaKey) {
            this.onVertexMarkerMetaKeyClick(e);
        } else if (index === e.vertex.getLastIndex() && this._drawing === Editable.FORWARD) {
            if (index >= this.MIN_VERTEX - 1) commit = true;
        } else if (index === 0 && this._drawing === Editable.BACKWARD && this._drawnLatLngs.length >= this.MIN_VERTEX) {
            commit = true;
        } else if (index === 0 && this._drawing === Editable.FORWARD && this._drawnLatLngs.length >= this.MIN_VERTEX && this.CLOSED) {
            commit = true;  // Allow to close on first point also for polygons
        } else {
            this.onVertexRawMarkerClick(e);
        }
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:clicked: VertexEvent
        // Fired when a `click` is issued on a vertex, after all internal actions.
        this.fireAndForward('editable:vertex:clicked', e);
        if (commit) this.commitDrawing(e);
    },

    onVertexRawMarkerClick: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:rawclick: CancelableVertexEvent
        // Fired when a `click` is issued on a vertex without any special key and without being in drawing mode.
        this.fireAndForward('editable:vertex:rawclick', e);
        if (e._cancelled) return;
        if (!this.vertexCanBeDeleted(e.vertex)) return;
        e.vertex.delete();
    },

    vertexCanBeDeleted: function (vertex) {
        return vertex.latlngs.length > this.MIN_VERTEX;
    },

    onVertexDeleted: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:deleted: VertexEvent
        // Fired after a vertex has been deleted by user.
        this.fireAndForward('editable:vertex:deleted', e);
    },

    onVertexMarkerCtrlClick: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:ctrlclick: VertexEvent
        // Fired when a `click` with `ctrlKey` is issued on a vertex.
        this.fireAndForward('editable:vertex:ctrlclick', e);
    },

    onVertexMarkerShiftClick: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:shiftclick: VertexEvent
        // Fired when a `click` with `shiftKey` is issued on a vertex.
        this.fireAndForward('editable:vertex:shiftclick', e);
    },

    onVertexMarkerMetaKeyClick: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:metakeyclick: VertexEvent
        // Fired when a `click` with `metaKey` is issued on a vertex.
        this.fireAndForward('editable:vertex:metakeyclick', e);
    },

    onVertexMarkerAltClick: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:altclick: VertexEvent
        // Fired when a `click` with `altKey` is issued on a vertex.
        this.fireAndForward('editable:vertex:altclick', e);
    },

    onVertexMarkerContextMenu: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:contextmenu: VertexEvent
        // Fired when a `contextmenu` is issued on a vertex.
        this.fireAndForward('editable:vertex:contextmenu', e);
    },

    onVertexMarkerMouseDown: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:mousedown: VertexEvent
        // Fired when user `mousedown` a vertex.
        this.fireAndForward('editable:vertex:mousedown', e);
    },

    onVertexMarkerMouseOver: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:mouseover: VertexEvent
        // Fired when a user's mouse enters the vertex
        this.fireAndForward('editable:vertex:mouseover', e);
    },

    onVertexMarkerMouseOut: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:mouseout: VertexEvent
        // Fired when a user's mouse leaves the vertex
        this.fireAndForward('editable:vertex:mouseout', e);
    },

    onMiddleMarkerMouseDown: function (e) {
        // 🍂namespace Editable
        // 🍂section MiddleMarker events
        // 🍂event editable:middlemarker:mousedown: VertexEvent
        // Fired when user `mousedown` a middle marker.
        this.fireAndForward('editable:middlemarker:mousedown', e);
    },

    onVertexMarkerDrag: function (e) {
        this.onMove(e);
        if (this.feature._bounds) this.extendBounds(e);
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:drag: VertexEvent
        // Fired when a vertex is dragged by user.
        this.fireAndForward('editable:vertex:drag', e);
    },

    onVertexMarkerDragStart: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:dragstart: VertexEvent
        // Fired before a vertex is dragged by user.
        this.fireAndForward('editable:vertex:dragstart', e);
    },

    onVertexMarkerDragEnd: function (e) {
        // 🍂namespace Editable
        // 🍂section Vertex events
        // 🍂event editable:vertex:dragend: VertexEvent
        // Fired after a vertex is dragged by user.
        this.fireAndForward('editable:vertex:dragend', e);
    },

    setDrawnLatLngs: function (latlngs) {
        this._drawnLatLngs = latlngs || this.getDefaultLatLngs();
    },

    startDrawing: function () {
        if (!this._drawnLatLngs) this.setDrawnLatLngs();
        BaseEditor.prototype.startDrawing.call(this);
    },

    startDrawingForward: function () {
        this.startDrawing();
    },

    endDrawing: function () {
        this.tools.detachForwardLineGuide();
        this.tools.detachBackwardLineGuide();
        if (this._drawnLatLngs && this._drawnLatLngs.length < this.MIN_VERTEX) this.deleteShape(this._drawnLatLngs);
        BaseEditor.prototype.endDrawing.call(this);
        delete this._drawnLatLngs;
    },

    addLatLng: function (latlng) {
        if (this._drawing === Editable.FORWARD) this._drawnLatLngs.push(latlng);
        else this._drawnLatLngs.unshift(latlng);
        this.feature._bounds.extend(latlng);
        var vertex = this.addVertexMarker(latlng, this._drawnLatLngs);
        this.onNewVertex(vertex);
        this.refresh();
    },

    newPointForward: function (latlng) {
        this.addLatLng(latlng);
        this.tools.attachForwardLineGuide();
        this.tools.anchorForwardLineGuide(latlng);
    },

    newPointBackward: function (latlng) {
        this.addLatLng(latlng);
        this.tools.anchorBackwardLineGuide(latlng);
    },

    // 🍂namespace PathEditor
    // 🍂method push()
    // Programmatically add a point while drawing.
    push: function (latlng) {
        if (!latlng) return console.error('L.Editable.PathEditor.push expect a valid latlng as parameter');
        if (this._drawing === Editable.FORWARD) this.newPointForward(latlng);
        else this.newPointBackward(latlng);
    },

    removeLatLng: function (latlng) {
        latlng.__vertex.delete();
        this.refresh();
    },

    // 🍂method pop(): L.LatLng or null
    // Programmatically remove last point (if any) while drawing.
    pop: function () {
        if (this._drawnLatLngs.length <= 1) return;
        var latlng;
        if (this._drawing === Editable.FORWARD) latlng = this._drawnLatLngs[this._drawnLatLngs.length - 1];
        else latlng = this._drawnLatLngs[0];
        this.removeLatLng(latlng);
        if (this._drawing === Editable.FORWARD) this.tools.anchorForwardLineGuide(this._drawnLatLngs[this._drawnLatLngs.length - 1]);
        else this.tools.anchorForwardLineGuide(this._drawnLatLngs[0]);
        return latlng;
    },

    processDrawingClick: function (e) {
        if (e.vertex && e.vertex.editor === this) return;
        if (this._drawing === Editable.FORWARD) this.newPointForward(e.latlng);
        else this.newPointBackward(e.latlng);
        this.fireAndForward('editable:drawing:clicked', e);
    },

    onDrawingMouseMove: function (e) {
        BaseEditor.prototype.onDrawingMouseMove.call(this, e);
        if (this._drawing) {
            this.tools.moveForwardLineGuide(e.latlng);
            this.tools.moveBackwardLineGuide(e.latlng);
        }
    },

    refresh: function () {
        this.feature.redraw();
        this.onEditing();
    },

    // 🍂namespace PathEditor
    // 🍂method newShape(latlng?: L.LatLng)
    // Add a new shape (Polyline, Polygon) in a multi, and setup up drawing tools to draw it;
    // if optional `latlng` is given, start a path at this point.
    newShape: function (latlng) {
        var shape = this.addNewEmptyShape();
        if (!shape) return;
        this.setDrawnLatLngs(shape[0] || shape);  // Polygon or polyline
        this.startDrawingForward();
        // 🍂namespace Editable
        // 🍂section Shape events
        // 🍂event editable:shape:new: ShapeEvent
        // Fired when a new shape is created in a multi (Polygon or Polyline).
        this.fireAndForward('editable:shape:new', {shape: shape});
        if (latlng) this.newPointForward(latlng);
    },

    deleteShape: function (shape, latlngs) {
        var e = {shape: shape};
        Editable.makeCancellable(e);
        // 🍂namespace Editable
        // 🍂section Shape events
        // 🍂event editable:shape:delete: CancelableShapeEvent
        // Fired before a new shape is deleted in a multi (Polygon or Polyline).
        this.fireAndForward('editable:shape:delete', e);
        if (e._cancelled) return;
        shape = this._deleteShape(shape, latlngs);
        if (this.ensureNotFlat) this.ensureNotFlat();  // Polygon.
        this.feature.setLatLngs(this.getLatLngs());  // Force bounds reset.
        this.refresh();
        this.reset();
        // 🍂namespace Editable
        // 🍂section Shape events
        // 🍂event editable:shape:deleted: ShapeEvent
        // Fired after a new shape is deleted in a multi (Polygon or Polyline).
        this.fireAndForward('editable:shape:deleted', {shape: shape});
        return shape;
    },

    _deleteShape: function (shape, latlngs) {
        latlngs = latlngs || this.getLatLngs();
        if (!latlngs.length) return;
        var self = this,
            inplaceDelete = function (latlngs, shape) {
                // Called when deleting a flat latlngs
                shape = latlngs.splice(0, Number.MAX_VALUE);
                return shape;
            },
            spliceDelete = function (latlngs, shape) {
                // Called when removing a latlngs inside an array
                latlngs.splice(latlngs.indexOf(shape), 1);
                if (!latlngs.length) self._deleteShape(latlngs);
                return shape;
            };
        if (latlngs === shape) return inplaceDelete(latlngs, shape);
        for (var i = 0; i < latlngs.length; i++) {
            if (latlngs[i] === shape) return spliceDelete(latlngs, shape);
            else if (latlngs[i].indexOf(shape) !== -1) return spliceDelete(latlngs[i], shape);
        }
    },

    // 🍂namespace PathEditor
    // 🍂method deleteShapeAt(latlng: L.LatLng): Array
    // Remove a path shape at the given `latlng`.
    deleteShapeAt: function (latlng) {
        var shape = this.feature.shapeAt(latlng);
        if (shape) return this.deleteShape(shape);
    },

    // 🍂method appendShape(shape: Array)
    // Append a new shape to the Polygon or Polyline.
    appendShape: function (shape) {
        this.insertShape(shape);
    },

    // 🍂method prependShape(shape: Array)
    // Prepend a new shape to the Polygon or Polyline.
    prependShape: function (shape) {
        this.insertShape(shape, 0);
    },

    // 🍂method insertShape(shape: Array, index: int)
    // Insert a new shape to the Polygon or Polyline at given index (default is to append).
    insertShape: function (shape, index) {
        this.ensureMulti();
        shape = this.formatShape(shape);
        if (typeof index === 'undefined') index = this.feature._latlngs.length;
        this.feature._latlngs.splice(index, 0, shape);
        this.feature.redraw();
        if (this._enabled) this.reset();
    },

    extendBounds: function (e) {
        this.feature._bounds.extend(e.vertex.latlng);
    },

    onDragStart: function (e) {
        this.editLayer.clearLayers();
        BaseEditor.prototype.onDragStart.call(this, e);
    },

    onDragEnd: function (e) {
        this.initVertexMarkers();
        BaseEditor.prototype.onDragEnd.call(this, e);
    }

});

export default PathEditor;
