import Cache from '../../../util/Cache';
import GDrawGeomerty from '../avoid/GDrawGeomerty';
import {
    getParamJSON,
    getBufferData
} from './../../../util/es6-promise';
import LabelDrawer from './LabelDrawer';
import GAnnoAvoid from './../avoid/GAnnoAvoid';
import ParseLabelData from './../avoid/ParseLabelData';
import GridFilterLabel from './../avoid/GridFilterLabel';
import VarintReader from './../VarintReader';

class CanvasLayer {
    constructor() {
        this.width = 0;
        this.height = 0;

        //当前屏幕的瓦片层行列号集合
        this.grid = [];
        this.cache = new Cache(256);
        //注记图层对象
        this.gwvtAnno = null;
        //数据源集合
        this.dataSource = [];
        //如果dataSource是urldatasource,那么样式纹理是否加载完成。 如果只有localDataSource,则为true
        this.isReady = false;

        //地图的最大范围
        this.maxExtent = [];
        //地图的当前视口
        this.extent = [];
        //地图的当前分辨率
        this.res = 0;
        //瓦片大小
        this.tileSize = 256;
        //是否允许拾取
        this.hitDetection = false;
        //当前屏幕内的features
        this.features = [];
        //正在请求中的瓦片请求集合,还没返回的请求
        this.requestingTiles = {};
        // 是否支持有isImportant属性
        this.hasImportant = true;
        //缩放比例
        this.ratio = 1;
        let canvas = document.createElement('CANVAS');
        this.ctx = canvas.getContext('2d', {
            isQuality: true
        });
        // //是否已经设置过全局样式
        // this.isSetGlobalStyle = false;
        //全局是否开启避让
        this.ableAvoid = true;
        //全局是否开启权重排序
        this.ableWeight = true;

        //过滤格网大小
        this.cellsize = 4;
        //网格内保留点的个数
        this.maxPerCell = 1;

        //样式map
        this.styleMap = {};

        //避让调试工具模式
        this.debug = false;
        this.GAnnoAvoid = new GAnnoAvoid(this.ctx, false, this.debug);

        this.highlightLabels = {};
    }

    setDebug(debug) {
        this.debug = debug;
        this.GAnnoAvoid = new GAnnoAvoid(this.ctx, false, this.debug);
    }

    /**
     * 初始化
     */
    init(w, h, tileSize, gwvtAnno) {
        this.tileSize = tileSize;
        this.gwvtAnno = gwvtAnno;
        this.cellsize = gwvtAnno.cellsize;
        this.initCanvas(w, h);
        this.loadResources();
    };



    /**
     * 加载dataSource的样式文件和纹理，所有dataSource的
     * 样式文件和纹理加载完成，则isReady设置为ture
     */
    loadResources() {
        if (this.dataSource.length == 0) {
            this.isReady = false;
            return;
        }

        this.cache.clean();
        this.requestingTiles = {};
        let reqArr = [];
        for (let i = 0; i < this.dataSource.length; i++) {
            let ds = this.dataSource[i];
            if (ds.type == 'URLDataSource') {
                reqArr = reqArr.concat(ds.loadStyle());
            }
            if (ds.type == 'LocalDataSource') {
                reqArr = reqArr.concat(ds.loadTexture());
            }
        }

        if (reqArr.length > 0) {
            Promise.all(reqArr).then(function() {
                this.isReady = true;
                //重新请求注记数据
                if (this.grid.length > 0) {
                    this.requestLabelTiles(this.grid);
                }
            }.bind(this));
        } else {
            this.isReady = true;
        }
    };

    /**
     * 初始化画布
     * Parameters :
     * w - 图层宽
     * h - 图层高
     */
    initCanvas(w, h) {
        this.width = w;
        this.height = h;
        if (!this.root) {
            this.root = document.createElement("canvas");
        }
        this.root.style.width = this.width + "px";
        this.root.style.height = this.height + "px";
        this.root.width = this.width * this.ratio;
        this.root.height = this.height * this.ratio;
        this.root.id = 'labelCanvas';
        this.canvas = this.root.getContext("2d", {
            isQuality: true
        });


        if (this.hitDetection) {
            if (!this.hitCanvas) {
                this.hitCanvas = document.createElement("canvas");
            }
            this.hitCanvas.style.width = this.width + "px";
            this.hitCanvas.style.height = this.height + "px";
            this.hitCanvas.width = this.width;
            this.hitCanvas.height = this.height;
            this.hitContext = this.hitCanvas.getContext("2d", {
                isQuality: true
            });
        }
    };


    /**
     * 添加数据源
     * Parameters :
     * dataSource
     */
    addDataSource(dataSource) {
        if (dataSource.type == 'URLDataSource') {
            dataSource.url = dataSource.url + '&tilesize=' + this.tileSize;
        }

        if (dataSource.type == 'URLDataSource' || dataSource.type == 'LocalDataSource') {
            this.dataSource.push(dataSource);
        }
    };

    /**
     * 根据dataSoucceId移除数据源
     * Parameters :
     * dataSoucceId
     */
    removeDataSourceById(dataSoucceId) {
        for (let i = 0; i < this.dataSource.length; i++) {
            if (this.dataSource[i].id == dataSoucceId) {
                this.dataSource.splice(i, 1);
                return;
            }
        }
    };

    /**
     * 根据dataSoucceId获取数据源
     * Parameters :
     * dataSoucceId
     */
    getDataSourceById(dataSoucceId) {
        for (let i = 0; i < this.dataSource.length; i++) {
            if (this.dataSource[i].id == dataSoucceId) {
                return this.dataSource[i];
            }
        }
    };

    /**
     * 清空画布
     */
    clean() {
        this.canvas.clearRect(0, 0, this.width * this.ratio, this.height * this.ratio);
        if (this.hitContext) {
            this.hitContext.clearRect(0, 0, this.width, this.height);
        }
    };

    /**
     * 重新绘制注记要素，当动态更改DataSouce数据源后，需要调用redraw方法
     */
    redraw() {
        if (this.grid.length == 0) {
            return;
        }
        //重置styleMap
        this.styleMap = {};
        //重新加载样式，纹理文件
        this.loadResources();

    };

    /**
     * 请求注记瓦片
     * Parameters :
     * grid - 当前视口内，瓦片的层行列号集合
     * zoomChanged - 是否进行了缩放操作
     */
    requestLabelTiles(grid, zoomChanged) {
        this.grid = grid;
        //如果数据源没有准备好
        if (!this.isReady) {
            return;
        }

        //获取需要请求的url
        let requestTileUrls = this.getRequestTileUrls(grid);
        this.sendRequest(requestTileUrls);
    };

    /**
     * 获取localDataSource中在当前屏幕范围内的注记要素
     */
    getLocalLabelDatas() {
        let localFeatures = [];
        for (let i = 0; i < this.dataSource.length; i++) {
            let ds = this.dataSource[i];
            if (ds.type == 'LocalDataSource') {
                for (let j = 0; j < ds.features.length; j++) {
                    let feature = ds.features[j];
                    //找出在当前视口内的要素
                    if (feature.inBounds(this.extent)) {
                        feature.attributeId = feature.attributes['attributeId'];
                        feature.primaryId = feature.attributeId;

                        if (feature.type == 1) {
                            //转换要素的地理坐标为屏幕坐标
                            feature.sourceAngleData = [
                                [feature.sourceData, 0]
                            ];
                            feature.transformData(this.extent, this.res);
                            feature.label = feature.getFeatureLabel();
                            // feature.textures = ds.textures;
                            feature.iconImg = ds.textures[feature.style.texture];
                            localFeatures.push(feature);
                        }

                        if (feature.type == 2) {
                            feature.label = feature.getFeatureLabel();
                            feature.textures = ds.textures;
                            localFeatures = localFeatures.concat(this.cutLineFeature(feature, true));
                        }
                    }
                }
            }
        }

        return localFeatures;
    };

    /**
     * 计算需要请求的瓦片的url
     * Parameters :
     * requestTiles - 需要请求的瓦片层行列号集合
     */
    getRequestTileUrls(grid) {
        this.hitCacheUrls = [];
        this.currentTileDatas = [];
        this.defaultStyeTileDatas = [];
        //本次需要请求的url
        let requestTileUrls = {};
        //请求队列中找到的url集合
        let findedRequestUrls = {};
        for (let i = 0; i < this.dataSource.length; i++) {
            let dataSource = this.dataSource[i];
            //url数据源
            if (dataSource.type == 'URLDataSource') {
                let url = dataSource.url;
                for (let j = 0; j < grid.length; j++) {
                    let item = grid[j];
                    let tileUrl = url.replace('${x}', item.col).replace('{x}', item.col);
                    tileUrl = tileUrl.replace('${y}', item.row).replace('{y}', item.row);
                    tileUrl = tileUrl.replace('${z}', item.level).replace('{z}', item.level);

                    //多域名url
                    if (dataSource.urlArray.length > 0) {
                        let len = dataSource.urlArray.length - 1;
                        let index = Math.round(Math.random() * len);
                        let domainUrl = dataSource.urlArray[index];

                        let array = tileUrl.split('/mapserver');
                        let partUrl = array[1];
                        tileUrl = domainUrl + '/mapserver' + partUrl;
                    }

                    //判断缓存中有没有该注记
                    let cacheItem = this.cache.getItem(tileUrl);
                    if (cacheItem) {
                        this.hitCacheUrls.push({
                            url: tileUrl,
                            isDefaultStyle: dataSource.styleId == '_default__'
                        });
                    } else {
                        //已经发送的请求队列中找,队列中没找到的需要发送请求
                        if (!this.requestingTiles[tileUrl]) {
                            requestTileUrls[tileUrl] = {
                                url: tileUrl,
                                xyz: item,
                                dataSourceId: dataSource.id,
                                dataType: dataSource.newEngine ? 'arraybuffer' : "json"
                            };
                        } else {
                            findedRequestUrls[tileUrl] = true;
                        }
                    }
                }
            }
        }

        // console.log('total count  ================='+ grid.length);
        //关闭上次不需要的请求
        this.cancelRequest(findedRequestUrls);
        return requestTileUrls;
    };

    /**
     * 取消上次不需要的请求
     * Parameters :
     * findedRequestUrls - 请求队列中找到的url集合
     */
    cancelRequest(findedRequestUrls) {
        for (let tileUrl in this.requestingTiles) {
            if (!findedRequestUrls[tileUrl]) {
                let requestTile = this.requestingTiles[tileUrl];
                delete this.requestingTiles[tileUrl];
                requestTile.xhr.abort();
                requestTile.requestItem.cancel = true;
            }
        }
    };

    /**
     * 发送请求，取注记瓦片数据
     * Parameters :
     * requestTileUrls - 需要请求的瓦片url集合
     */
    sendRequest(requestTileUrls) {
        let count = 0;
        for (let url in requestTileUrls) {
            let item = requestTileUrls[url];
            let promise = null;
            if (item.dataType == "json") {
                promise = getParamJSON(item);
            } else if (item.dataType == "arraybuffer") {
                promise = getBufferData(item);
            }
            this.requestingTiles[item.url] = {
                xhr: promise.xhr,
                requestItem: item
            };
            promise.then(this.tileSuccessFunction.bind(this), this.tileFailFunction.bind(this));
            count++;
        }
        // console.log('sendRequest count ==============='+count);
        if (count == 0) {
            this.sendSuccess([], []);
        }
    };

    toBuffer(ab) {
        var buf = new Buffer(ab.byteLength);
        var view = new Uint8Array(ab);
        for (var i = 0; i < buf.length; ++i) {
            buf[i] = view[i];
        }
        return buf;
    };

    Utf8ArrayToStr(array) {
        var out, i, len, c;
        var char2, char3;

        out = "";
        len = array.length;
        i = 0;
        while (i < len) {
            c = array[i++];
            switch (c >> 4) {
                case 0:
                case 1:
                case 2:
                case 3:
                case 4:
                case 5:
                case 6:
                case 7:
                    // 0xxxxxxx
                    out += String.fromCharCode(c);
                    break;
                case 12:
                case 13:
                    // 110x xxxx   10xx xxxx
                    char2 = array[i++];
                    out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
                    break;
                case 14:
                    // 1110 xxxx  10xx xxxx  10xx xxxx
                    char2 = array[i++];
                    char3 = array[i++];
                    out += String.fromCharCode(((c & 0x0F) << 12) |
                        ((char2 & 0x3F) << 6) |
                        ((char3 & 0x3F) << 0));
                    break;
            }
        }

        return out;
    }

    /**
     * 单个瓦片注记请求成功的回调
     */
    tileSuccessFunction(data) {
        if (data.param.cancel == true || this.isEmptyObject(this.requestingTiles)) {
            //请求取消失败的，直接返回
            return;
        }

        //var buf = Buffer.from(data.data);        
        // var buf = this.toBuffer(data.data);
        // if (buf.length > 0) {
        //     var vant = new VarintReader(buf, 4);
        //     var layerNameArr = vant.getAllLayerNames();
        // }
        // return;

        let url = data.param.url;
        //删除正在请求的url
        delete this.requestingTiles[url];

        let dataSourceId = data.param.dataSourceId;
        let dataSource = this.getDataSourceById(dataSourceId);
        let features = null;
        if (dataSource && dataSource.newEngine) {
            var buf = this.toBuffer(data.data);
            var dataObj = {
                param: data.param,
                tileData: {}
            };
            if (buf.length > 0) {
                var vant = new VarintReader(buf, 4);
                var layerNameArr = vant.getAllLayerNames();
                for (var i = 0; i < layerNameArr.length; i++) {
                    dataObj.tileData[layerNameArr[i]] = {
                        geometryType: vant.getGeometryType(layerNameArr[i]),
                        props: JSON.parse(this.Utf8ArrayToStr(vant.getLayerPro(layerNameArr[i])))
                    }
                }
                dataObj["vant"] = vant;
            }
            features = this.parseFeature(dataObj, dataSource);
        } else {
            features = this.parseFeature(data, dataSource);
        }

        let labelFeatures = [];

        if (features.styleId == null) {
            this.currentTileDatas.push({
                url: url,
                labelFeatures: [],
                avoidLineFeatures: features.avoidLineFeatures
            });
            return;
        }

        if (features.styleId == '_default__') {
            ParseLabelData.updateFeatureAttr(features.pointFeatures, this.styleMap, this.ratio, {}, true, this.maxExtent, this.extent, this.res, this.tileSize);
            labelFeatures = labelFeatures.concat(features.lineFeatures);
            labelFeatures = labelFeatures.concat(features.pointFeatures);
            this.defaultStyeTileDatas.push({
                url: url,
                labelFeatures: labelFeatures,
                avoidLineFeatures: features.avoidLineFeatures
            });
        } else {
            //如果需要避让
            if (this.ableAvoid) {
                // console.time('fristFilter');
                //第一次格网过滤
                labelFeatures = GridFilterLabel.fristFilter(features.pointFeatures, features.lineFeatures,
                    this.styleMap, this.ableWeight, true, this.tileSize, this.cellsize, this.tileSize * 1.0, this.maxPerCell);

                //第一次过滤后的点注记，设置更多的属性
                ParseLabelData.updateFeatureAttr(labelFeatures.pointFeatures, this.styleMap, this.ratio, dataSource.textures,
                    true, this.maxExtent, this.extent, this.res, this.tileSize);
                // console.timeEnd('fristFilter');
                //第二次格网过滤
                // console.time('scendFilter');
                labelFeatures = GridFilterLabel.scendFilter(labelFeatures.pointFeatures, labelFeatures.lineFeatures, this.styleMap, this.ableWeight, true, this.tileSize, this.cellsize, this.tileSize * 1.0);
                // console.timeEnd('scendFilter');

                //设置boxs
                labelFeatures = this.GAnnoAvoid.GLabelBox.setBox(labelFeatures, this.styleMap, false);
                //第三次过滤去重
                // labelFeatures = GridFilterLabel.threeFilter(labelFeatures,this.styleMap,this.tileSize);

                if (features.avoidLineFeatures.length > 0) {
                    // //设置boxs
                    // labelFeatures = this.GAnnoAvoid.GLabelBox.setBox(labelFeatures,this.styleMap,false);
                    labelFeatures = this.GAnnoAvoid.avoid(labelFeatures, this.styleMap, features.avoidLineFeatures);
                }


                // ParseLabelData.updateFeatureAttr(features.pointImportantFeatures,this.styleMap,this.ratio,dataSource.textures,
                //     true,this.maxExtent,this.extent,this.res,this.tileSize);

            } else {

                ParseLabelData.updateFeatureAttr(features.pointFeatures, this.styleMap, this.ratio, dataSource.textures,
                    true, this.maxExtent, this.extent, this.res, this.tileSize);

                // ParseLabelData.updateFeatureAttr(features.pointImportantFeatures,this.styleMap,this.ratio,dataSource.textures,
                //     true,this.maxExtent,this.extent,this.res,this.tileSize);

                labelFeatures = features.pointFeatures.concat(features.lineFeatures);
            }

            this.currentTileDatas.push({
                url: url,
                labelFeatures: labelFeatures,
                avoidLineFeatures: features.avoidLineFeatures
            });
        }

        //如果所有的瓦片请求成功
        if (this.isEmptyObject(this.requestingTiles)) {
            this.sendSuccess(this.currentTileDatas, this.defaultStyeTileDatas);
        }
    };

    //判断map是否为空
    isEmptyObject(e) {
        for (let t in e)
            return !1;
        return !0
    };

    /**
     * 单个瓦片注记请求失败的回调
     */
    tileFailFunction(data) {
        if (data.param.cancel == true || this.isEmptyObject(this.requestingTiles)) {
            //请求取消失败的，直接返回
            return;
        }

        let url = data.param.url;
        delete this.requestingTiles[url];

        //如果所有的瓦片请求成功
        if (this.isEmptyObject(this.requestingTiles)) {
            this.sendSuccess(this.currentTileDatas, this.defaultStyeTileDatas);
        }
    };


    /**
     * 请求成功的回调函数，没有请求url，也会执行该回调
     * Parameters :
     * results - 请求成功的结果
     */
    sendSuccess(results, defaultStyleResult) {
        if (this.gwvtAnno.animating) {
            return;
        }
        //重置图层位置
        this.gwvtAnno.resetCanvasDiv();
        this.clean();
        //保证当前屏幕内需要拾取的要素
        if (this.hitDetection) {
            this.features = [];
        }

        this.drawLabel(results);
        this.drawDefaultStyleLabel(defaultStyleResult);
    };


    /**
     * 合并本地注记，绘制注记 (默认样式的注记)
     * Parameters :
     * results - 请求成功的结果
     */
    drawDefaultStyleLabel(results) {
        //合并上次在当前视口范围内的注记要素(不包括本地要素)
        let mergeFeatures = this.mergeLabelData(results, this.hitCacheUrls, true);
        let labelFeatures = mergeFeatures.labelFeatures;

        for (let i = 0; i < results.length; i++) {
            let item = results[i];
            this.cache.push(item.url, item);
        }

        //保证当前屏幕内需要拾取的要素
        if (this.hitDetection) {
            for (let i = 0; i < labelFeatures.length; i++) {
                let feature = labelFeatures[i];
                this.features[feature.id] = feature;
            }
        }

        //绘制注记要素
        GDrawGeomerty.drawDefaultStyle(this.canvas, labelFeatures, this.styleMap, this.ratio, false, false, this.hitContext, this.hitDetection);
    }


    /**
     * 合并本地注记，注记避让，绘制注记
     * Parameters :
     * results - 请求成功的结果
     */
    drawLabel(results) {
        //合并上次在当前视口范围内的注记要素(不包括本地要素)
        let mergeFeatures = this.mergeLabelData(results, this.hitCacheUrls, false);
        let labelFeatures = mergeFeatures.labelFeatures;

        for (let i = 0; i < results.length; i++) {
            let item = results[i];
            this.cache.push(item.url, item);
        }

        //获取localDataSource中在当前屏幕范围内的注记要素
        let localFeatures = this.getLocalLabelDatas();
        labelFeatures = labelFeatures.concat(localFeatures);


        //设置boxs
        labelFeatures = this.GAnnoAvoid.GLabelBox.setBox(labelFeatures, this.styleMap, true);

        let avoidlabelDatas = labelFeatures;
        if (this.ableAvoid) {
            avoidlabelDatas = this.GAnnoAvoid.defaultAvoid(labelFeatures, this.styleMap, true, this.hasImportant, this.ableWeight);
        }

        // this.drawAvoidLine(mergeFeatures.avoidLineFeatures);
        //保证当前屏幕内需要拾取的要素
        if (this.hitDetection) {
            for (let i = 0; i < avoidlabelDatas.length; i++) {
                let feature = avoidlabelDatas[i];
                this.features[feature.id] = feature;
            }
        }

        //绘制注记要素
        GDrawGeomerty.draw(this.canvas, avoidlabelDatas, this.styleMap, this.ratio, false, false, this.hitContext, this.hitDetection, false, this.debug);

        //绘制道路线
        // GDrawGeomerty.drawLines(this.canvas,avoidlabelDatas,this.styleMap,this.ratio,false,false,this.hitContext, this.hitDetection,this);
    }

    drawAvoidLine(avoidLineFeatures) {
        this.canvas.save();
        this.canvas.beginPath();
        for (let i = 0; i < avoidLineFeatures.length; i++) {
            let avoidLineFeature = avoidLineFeatures[i];
            this.canvas.lineWidth = 1;
            this.canvas.strokeStyle = "#fff000";
            //画线
            this.canvas.moveTo(avoidLineFeature.datas[0], avoidLineFeature.datas[1]);
            for (let j = 1; j < avoidLineFeature.datas.length / 2; j++) {
                this.canvas.lineTo(avoidLineFeature.datas[j * 2], avoidLineFeature.datas[j * 2 + 1]);
            }
        }
        this.canvas.stroke();
        this.canvas.restore();
    };

    /**
     * 解析返回的注记信息
     * Parameters:
     * tileData - 请求返回的注记数据
     * Returns:
     * labelDatas - 设置过样式,坐标由瓦片内坐标转为屏幕坐标的注记数据
     */
    parseFeature(tileData, dataSource) {
        let layers = {};
        if (dataSource.newEngine) {
            var sTileDataObj = tileData.tileData;
            for (let key in sTileDataObj) {
                layers[key] = {
                    features: [],
                    fieldsConfig: dataSource.serverInfo[key].fieldsConfig,
                    serverId: dataSource.servername,
                    type: 1
                };
                if (sTileDataObj[key].geometryType.toLowerCase() == "point") {
                    layers[key].type = 1;
                } else if (sTileDataObj[key].geometryType.toLowerCase() == "line" ||
                    sTileDataObj[key].geometryType.toLowerCase() == "linestring" || sTileDataObj[key].geometryType.toLowerCase() == "multilinestring") {
                    layers[key].type = 2;
                }
                if (sTileDataObj[key].props && sTileDataObj[key].props.length > 0) {
                    for (var k = 0; k < sTileDataObj[key].props.length; k++) {
                        var tDataArr = [];
                        tDataArr.push(sTileDataObj[key].geometryType);
                        tDataArr.push(sTileDataObj[key].props[k]);
                        tDataArr.push(tileData.vant.getCoordinatesByIndex(key, k, 10));
                        layers[key].features.push(tDataArr);
                    }

                }
            }
        } else {
            layers = tileData.data;
        }
        let xyz = {
            x: tileData.param.xyz.col,
            y: tileData.param.xyz.row,
            l: tileData.param.xyz.level
        };
        let count = 0;
        for (let key in layers) {
            let layerData = layers[key];
            count++;
        }

        let features = {
            pointFeatures: [],
            lineFeatures: [],
            pointImportantFeatures: [],
            lineImportantFeatures: []
        };
        if (count > 0 && dataSource && dataSource.styleFun) {
            //设置样式
            let itemDatas = [];
            let level = xyz.l;
            if (dataSource.newEngine) {
                let render = new LabelDrawer(layers, this.styleMap, level);
                dataSource.styleFun.call({}, render, level);
            } else {
                let drawer = new LabelDrawer(layers, this.styleMap, level);
                dataSource.styleFun.call({}, drawer, level);
            }
            //转换瓦片坐标为屏幕坐标,并构造label数据
            features = ParseLabelData.parseLayerDatas(
                layers, this.styleMap, xyz, true, this.maxExtent, this.extent, this.res, this.tileSize);
        }

        let avoidLineFeatures = ParseLabelData.parseAvoidLine(layers['_layerAvoids'], xyz, true, this.maxExtent, this.extent, this.res, this.tileSize);

        let pointFeatures = GridFilterLabel.removeTileOutPointFeatures(features.pointFeatures, this.tileSize);
        // let lineFeatures = GridFilterLabel.removeTileOutLineFeatures(features.lineFeatures,this.tileSize);
        let lineFeatures = features.lineFeatures;

        let styleId = dataSource ? dataSource.styleId : null;
        return {
            styleId: styleId,
            pointFeatures: pointFeatures,
            lineFeatures: lineFeatures,
            avoidLineFeatures: avoidLineFeatures
        };
    };

    setGlobalStyle(globalStyle) {
        this.ableAvoid = !globalStyle.disableAvoid;
        this.ableWeight = !globalStyle.disableWeight;
        this.boxDistance = globalStyle.boxDistance;
    }


    /**
     * 将本次请求的注记数据和上次在本视口范围内的要素合并
     * Returns:
     * labelDatas - 合并后的注记数据，当前视口整个屏幕的数据
     */
    mergeLabelData(results, hitCacheUrls, isDefaultStyle) {
        let labelFeatures = [];
        let avoidLineFeatures = [];
        for (let j = 0; j < results.length; j++) {
            let result = results[j];
            labelFeatures = labelFeatures.concat(result.labelFeatures);
            avoidLineFeatures = avoidLineFeatures.concat(result.avoidLineFeatures);
        }

        let urls = [];
        for (let m = 0; m < hitCacheUrls.length; m++) {
            let item = hitCacheUrls[m];
            if (item.isDefaultStyle == isDefaultStyle) {
                urls.push(item.url);
            }
        }


        for (let i = 0; i < urls.length; i++) {
            let cacheLabelFeatures = this.cache.getItem(urls[i]).labelFeatures;
            for (let j = 0; j < cacheLabelFeatures.length; j++) {
                let labelFeature = cacheLabelFeatures[j];
                //重新计算当前屏幕坐标
                labelFeature.datas = ParseLabelData.transformData(labelFeature.sourceAngleData, labelFeature.xyz, this.maxExtent, this.extent, this.res, this.tileSize);
            }
            labelFeatures = labelFeatures.concat(cacheLabelFeatures);

            let cacheAvoidLineFeatures = this.cache.getItem(urls[i]).avoidLineFeatures;
            for (let j = 0; j < cacheAvoidLineFeatures.length; j++) {
                let avoidLineFeature = cacheAvoidLineFeatures[j];
                //重新计算当前屏幕坐标
                avoidLineFeature.datas = ParseLabelData.transformAvoidLine(avoidLineFeature.sourceDatas, avoidLineFeature.xyz, this.maxExtent, this.extent, this.res, this.tileSize);
            }
            avoidLineFeatures = avoidLineFeatures.concat(cacheAvoidLineFeatures);
        }

        return {
            labelFeatures: labelFeatures,
            avoidLineFeatures: avoidLineFeatures
        };
    };

    /**
     * 根据屏幕坐标获取feature
     * Parameters :
     * x
     * y
     */
    getFeatureByXY(x, y) {
        let feature = null;
        if (this.hitDetection) {
            let featureId;
            let data = this.hitContext.getImageData(x, y, 1, 1).data;
            if (data[3] === 255) { // antialiased
                let id = data[2] + (256 * (data[1] + (256 * data[0])));
                if (id) {
                    featureId = id - 1;
                    try {
                        feature = this.features[featureId];
                    } catch (err) {
                        console.log(err);
                    }
                }
            }
        }
        return feature;
    }

    /**
     * 根据屏幕坐标获取feature, 可以拾取叠加的注记，效率比getFeatureByXY低
     * Parameters :
     * x
     * y
     */
    getFeaturesByXY(x, y) {
        let pickFeatures = [];
        for (let key in this.features) {
            let feature = this.features[key];
            if (feature.type == 1) {
                if (this.isInBox(x, y, feature.box)) {
                    pickFeatures.push(feature);
                    continue;
                }
            }
            if (feature.type == 2) {
                for (let j = 0; j < feature.boxs.length; j++) {
                    let box = feature.boxs[j];
                    if (this.isInBox(x, y, box)) {
                        pickFeatures.push(feature);
                        break;
                    }
                }
            }
        }
        return pickFeatures;
    }


    /**
     * 将线注记原始坐标带点和角度的格式，和切过的线的格式一致（针对默认样式的线主机）
     * Parameters:
     * line - 线注记原始数据
     * Returns:
     */
    lineToSourceAngleData(line) {
        let sourceAngleData = [];
        for (let i = 0; i < line.length; i++) {
            let x = line[i];
            let y = line[i + 1];
            sourceAngleData.push([
                [x, y], 0
            ]);
            i++;
        }
        return sourceAngleData;
    };

    /**
     * 将瓦片内坐标转换为当前屏幕坐标
     * Parameters:
     * points - 瓦片内坐标数组,item示例：[[12,20],0] [12,20]为点坐标，0为旋转的角度
     * xyz - 瓦片的层行列号
     * Returns:
     * rdata - 本地屏幕内坐标数组
     */
    transformData(points, xyz) {
        //取出当前视口左上角的地理坐标
        let left = this.extent[0];
        let top = this.extent[3];

        //地图最大的范围
        let mLeft = this.maxExtent[0];
        let mTop = this.maxExtent[3];

        //计算坐上角的屏幕坐标
        let x = (left - mLeft) / this.res;
        let y = (mTop - top) / this.res;

        let rPoint = [];

        for (let i = 0; i < points.length; i++) {
            let point = points[i][0];
            let gx = point[0] + xyz.x * this.tileSize;
            let gy = point[1] + xyz.y * this.tileSize;
            let p = [gx - x, gy - y];
            rPoint.push([p, points[i][1]]);
        }
        return rPoint;
    };


    isInBox(x, y, box) {
        if (x > box[0] && x < box[2] && y > box[1] && y < box[3]) {
            return true;
        }
        return false;
    };

    /**
     * 高亮指定的注记
     * Parameters :
     * featrueIds 需要高亮的注记id数组
     * lightColor 注记高亮的填充色
     */
    highlightLabel(featrueIds, lightColor) {
        for (let key in this.highlightLabels) {
            let feature = this.highlightLabels[key];
            delete feature['lightColor'];
        }

        this.highlightLabels = {};

        for (let i = 0; i < featrueIds.length; i++) {
            let id = featrueIds[i];
            let feature = this.features[id];
            feature.lightColor = lightColor;
            this.highlightLabels[id] = feature;
        }
    }
}
export default CanvasLayer;