import L from 'leaflet';
import '../core/Base';

export let ZoomBox = L.Control.extend({
    _map: null,
    includes: L.Evented ? L.Evented.prototype : L.Mixin.Events,
    onAdd: function (map) {
        this._map = map;
        this._container = L.DomUtil.create('div', 'leaflet-zoom-box-control leaflet-bar');
        // Bind to the map's boxZoom handler
        var _origMouseDown = map.boxZoom._onMouseDown;
        map.boxZoom._onMouseDown = function(e){
            if (e.button === 2) {return;}  // prevent right-click from triggering zoom box
            _origMouseDown.call(map.boxZoom, {
                clientX: e.clientX,
                clientY: e.clientY,
                which: 1,
                shiftKey: true
            });
        };

        //var _origMouseUp = map.boxZoom._onMouseUp;
        map.boxZoom._onMouseUp = function(e){
            if ((e.which !== 1) && (e.button !== 1)) { return; }

            this._finish();

            if (!this._moved) { return; }
            setTimeout(L.bind(this._resetState, this), 0);

            var startLatLng = this._map.containerPointToLatLng(this._startPoint);
            var endLatLng = this._map.containerPointToLatLng(this._point);
            var bounds = new L.LatLngBounds(startLatLng, endLatLng);
            var out = this.out;
            if(!out){
                this._map
                    .fitBounds(bounds)
                    .fire('boxzoomend', {boxZoomBounds: bounds});
            }else{
                var center = bounds.getCenter();
                var zoom = this._map.getZoom() - 1 >= 0 ? this._map.getZoom() - 1 : 0;
                this._map.setView(center, zoom)
                    .fire('boxzoomend', {boxZoomBounds: bounds});
            }
        }

        map.on('boxzoomend', this.deactivate, this);
        return this._container;
    },
    activate: function(options) {
        this._map.boxZoom.out = options.out;
        this._map.dragging.disable();
        this._map.boxZoom.addHooks();
        L.DomUtil.addClass(this._map.getContainer(), 'leaflet-zoom-box-crosshair');
    },
    deactivate: function() {
        this._map.dragging.enable();
        this._map.boxZoom.removeHooks();
        L.DomUtil.removeClass(this._map.getContainer(), 'leaflet-zoom-box-crosshair');
    }
});

export let zoomBox = function () {
    return new ZoomBox();
};

L.geoway.control.zoomBox = zoomBox;
L.geoway.control.ZoomBox = ZoomBox;
L.Control.ZoomBox = ZoomBox;
L.control.zoomBox = zoomBox;
