/**
 * Geoway Leaflet SDK
 */
import L from "leaflet";
import './Proj4Leaflet';

L.geoway = L.geoway || {};
L.geoway.control = L.geoway.control || {};
L.geoway.format = L.geoway.format || {};
L.geoway.service = L.geoway.service || {};

L.Map.mergeOptions({
    crs: L.CRS.EPSG4326,
    attributionControl : false,
    minZoom : 1
});

L.Map.include({
    getScales: function(){
        if(this.options.scales){
            return this.options.scales;
        }else{
            return this.defaultScales();
        }
    },
    defaultScales: function(){
        return [
            5.916587109091312E8,
            2.958293554545656E8,
            1.479146777272828E8,
            7.39573388636414E7,
            3.69786694318207E7,
            1.848933471591035E7,
            9244667.357955175,
            4622333.678977588,
            2311166.839488794,
            1155583.419744397,
            577791.7098721985,
            288895.85493609926,
            144447.92746804963,
            72223.96373402482,
            36111.98186701241,
            18055.990933506204,
            9027.995466753102,
            4513.997733376551,
            2256.998866688275,
            1128.4994333441375,
            564.2497166720685
        ];
    }
});
