import L from "leaflet";
import '../../core/Base';
import {WMSCapabilities_v11} from "./WMSCapabilities.v1_1";
import '../../util/Util';

export let WMSCapabilities_v111 = WMSCapabilities_v11.extend({

    version: "1.1.1",

    readers: {
        "wms": L.Util.applyDefaults({
            "SRS": function(node, obj) {
                obj.srs[this.getChildValue(node)] = true;
            }
        }, WMSCapabilities_v11.prototype.readers["wms"])
    }

});

L.geoway.format.WMSCapabilities.v1_1_1 = WMSCapabilities_v111;
