import BaseEditor from './BaseEditor';
import {EditableMixin, keepEditable} from "./Editable";
import L from 'leaflet';

// 🍂namespace Editable; 🍂class MarkerEditor
// 🍂inherits BaseEditor
// Editor for Marker.
let MarkerEditor = BaseEditor.extend({

    onDrawingMouseMove: function (e) {
        BaseEditor.prototype.onDrawingMouseMove.call(this, e);
        if (this._drawing) this.feature.setLatLng(e.latlng);
    },

    processDrawingClick: function (e) {
        // 🍂namespace Editable
        // 🍂section Drawing events
        // 🍂event editable:drawing:clicked: Event
        // Fired when user `click` while drawing, after all internal actions.
        this.fireAndForward('editable:drawing:clicked', e);
        this.commitDrawing(e);
    },

    connect: function (e) {
        // On touch, the latlng has not been updated because there is
        // no mousemove.
        if (e) this.feature._latlng = e.latlng;
        BaseEditor.prototype.connect.call(this, e);
    }

});

var MarkerMixin = {

    getEditorClass: function (tools) {
        return (tools && tools.options.markerEditorClass) ? tools.options.markerEditorClass : MarkerEditor;
    }

};

if (L.Marker) {
    L.Marker.include(EditableMixin);
    L.Marker.include(MarkerMixin);
    L.Marker.addInitHook(keepEditable);
}

export default MarkerEditor;
