import L from 'leaflet';
import '../../core/Base';
import TextMessage from './TextMessage';
import {DrawPolyline} from './DrawPolyline';
import '../../util/Util';

export let DrawPolygon = DrawPolyline.extend({
    statics: {
        TYPE: 'polygon'
    },

    Poly: L.Polygon,

    options: {
        showArea: false,
        shapeOptions: {
            stroke: true,
            color: '#f06eaa',
            weight: 4,
            opacity: 0.5,
            fill: true,
            fillColor: null, //same as color by default
            fillOpacity: 0.2,
            clickable: true
        },
        metric: true // Whether to use the metric measurement system or imperial
    },

    initialize: function (map, options) {
        this.type = DrawPolygon.TYPE;
        DrawPolyline.prototype.initialize.call(this, map, options);
    },

    _updateFinishHandler: function () {
        var markerCount = this._markers.length;

        // The first marker should have a click handler to close the polygon
        if (markerCount === 1) {
            this._markers[0].on('click', this._finishShape, this);
        }

        // Add and update the double click handler
        if (markerCount > 2) {
            this._markers[markerCount - 1].on('dblclick', this._finishShape, this);
            // Only need to remove handler if has been added before
            if (markerCount > 3) {
                this._markers[markerCount - 2].off('dblclick', this._finishShape, this);
            }
        }
    },

    _getTooltipText: function () {
        var text, subtext;

        if (this._markers.length === 0) {
            text = TextMessage.draw.handlers.polygon.tooltip.start;
        } else if (this._markers.length < 3) {
            text = TextMessage.draw.handlers.polygon.tooltip.cont;
        } else {
            text = TextMessage.draw.handlers.polygon.tooltip.end;
            subtext = this._getMeasurementString();
        }

        return {
            text: text,
            subtext: subtext
        };
    },

    _getMeasurementString: function () {
        var area = this._area;

        if (!area) {
            return null;
        }

        return L.Util.readableArea(area, this.options.metric);
    },

    _shapeIsValid: function () {
        return this._markers.length >= 3;
    },

    _vertexChanged: function (latlng, added) {
        var latLngs;

        // Check to see if we should show the area
        if (!this.options.allowIntersection && this.options.showArea) {
            latLngs = this._poly.getLatLngs();

            this._area = L.Util.geodesicArea(latLngs);
        }

        DrawPolyline.prototype._vertexChanged.call(this, latlng, added);
    },

    _cleanUpShape: function () {
        var markerCount = this._markers.length;

        if (markerCount > 0) {
            this._markers[0].off('click', this._finishShape, this);

            if (markerCount > 2) {
                this._markers[markerCount - 1].off('dblclick', this._finishShape, this);
            }
        }
    }
});

export let drawPolygon =  function (map, options) {
    return new DrawPolygon(map, options);
}

L.geoway.control.drawPolygon = drawPolygon;
L.geoway.control.DrawPolygon = DrawPolygon;
L.Draw = L.Draw ? L.Draw : {};
L.Draw.Polygon = DrawPolygon;
