import L from 'leaflet';
import '../core/Base';
import FetchRequest from '../util/FetchRequest';

export let ImageMapLayer = L.TileLayer.extend({
    initialize: function(url, options){
        this._url = url;
        L.setOptions(this, options);
    },
    getTileUrl: function (coords) {
        var data = {
            r: L.Browser.retina ? '@2x' : '',
            s: this._getSubdomain(coords),
            x: coords.x,
            y: coords.y,
            z: this._getZoomForUrl()
        };
        if (this._map && !this._map.options.crs.infinite) {
            var invertedY = this._globalTileRange.max.y - coords.y;
            if (this.options.tms) {
                data['y'] = invertedY;
            }
            data['-y'] = invertedY;
        }
        L.extend(data, this.options);
        var url = this._url + "/map/data?tileSize=" + data.tileSize + "&row=" + data.y + "&col=" + data.x + "&level=" + data.z;
        if(data.style){
            url += "&style=" +  data.style;
        }
        if(data.code){
            url += "&code=" +  data.code;
        }
        return url;
    },
    /**
     * 渲染样式保存
     * */
    saveStyle: function(json, callback){
        var url = this._url + "/styleConfig";
        var resource = {styleconfigjson: json ? JSON.stringify(json) : ''};
        FetchRequest.post(url, resource).then(function(data){
            if(callback){
                callback(data);
            }
        });
    },
    /**
     * 渲染样式临时设置
     * 分四种渲染方式
     *  dem山体阴影
     * 	{"mode":"hillshade","Altidude":50,"Azimuth":210,"ZFactor":1}
     * 	dem坡向
     * 	{"mode":"slope","UsePercent":"true","Scale":15}
     * 	dem坡向
     * 	{"mode":"aspect","ZeroForFlat":"true"}
     * 	dom波段组合
     * 	{"mode":"composite","ReadBandIndex":"2,0,1"}
     *
     * */
    setStyle: function(json){
        this.options.style = encodeURIComponent(JSON.stringify(json));
        this.redraw();
    },
    setCode: function(code){
        this.options.code = code;
        this.redraw();
    },
    clearStyle: function(){
        this.options.style = null;
        this.options.code = null;
        this.redraw();
    }
});

export let imageMapLayer = function (url, options) {
    return new ImageMapLayer(url, options);
};

L.geoway.imageMapLayer = imageMapLayer;
L.geoway.ImageMapLayer = ImageMapLayer;
L.ImageMapLayer = ImageMapLayer;
L.tileLayer.image = imageMapLayer;
