import PathEditor from './PathEditor';
import {isFlat, EditableMixin} from "./Editable";
import L from 'leaflet';

// 🍂namespace Editable; 🍂class PolygonEditor
// 🍂inherits PathEditor
let PolygonEditor = PathEditor.extend({

    CLOSED: true,
    MIN_VERTEX: 3,

    newPointForward: function (latlng) {
        PathEditor.prototype.newPointForward.call(this, latlng);
        if (!this.tools.backwardLineGuide._latlngs.length) this.tools.anchorBackwardLineGuide(latlng);
        if (this._drawnLatLngs.length === 2) this.tools.attachBackwardLineGuide();
    },

    addNewEmptyHole: function (latlng) {
        this.ensureNotFlat();
        var latlngs = this.feature.shapeAt(latlng);
        if (!latlngs) return;
        var holes = [];
        latlngs.push(holes);
        return holes;
    },

    // 🍂method newHole(latlng?: L.LatLng, index: int)
    // Set up drawing tools for creating a new hole on the Polygon. If the `latlng` param is given, a first point is created.
    newHole: function (latlng) {
        var holes = this.addNewEmptyHole(latlng);
        if (!holes) return;
        this.setDrawnLatLngs(holes);
        this.startDrawingForward();
        if (latlng) this.newPointForward(latlng);
    },

    addNewEmptyShape: function () {
        if (this.feature._latlngs.length && this.feature._latlngs[0].length) {
            var shape = [];
            this.appendShape(shape);
            return shape;
        } else {
            return this.feature._latlngs;
        }
    },

    ensureMulti: function () {
        if (this.feature._latlngs.length && isFlat(this.feature._latlngs[0])) {
            this.feature._latlngs = [this.feature._latlngs];
        }
    },

    ensureNotFlat: function () {
        if (!this.feature._latlngs.length || isFlat(this.feature._latlngs)) this.feature._latlngs = [this.feature._latlngs];
    },

    vertexCanBeDeleted: function (vertex) {
        var parent = this.feature.parentShape(vertex.latlngs),
            idx = L.Util.indexOf(parent, vertex.latlngs);
        if (idx > 0) return true;  // Holes can be totally deleted without removing the layer itself.
        return PathEditor.prototype.vertexCanBeDeleted.call(this, vertex);
    },

    getDefaultLatLngs: function () {
        if (!this.feature._latlngs.length) this.feature._latlngs.push([]);
        return this.feature._latlngs[0];
    },

    formatShape: function (shape) {
        // [[1, 2], [3, 4]] => must be nested
        // [] => must be nested
        // [[]] => is already nested
        if (isFlat(shape) && (!shape[0] || shape[0].length !== 0)) return [shape];
        else return shape;
    }

});
var PolygonMixin = {

    getEditorClass: function (tools) {
        return (tools && tools.options.polygonEditorClass) ? tools.options.polygonEditorClass : PolygonEditor;
    },

    shapeAt: function (latlng, latlngs) {
        // We can have those cases:
        // - latlngs are just a flat array of latlngs, use this
        // - latlngs is an array of arrays of latlngs, this is a simple polygon (maybe with holes), use the first
        // - latlngs is an array of arrays of arrays, this is a multi, loop over
        var shape = null;
        latlngs = latlngs || this._latlngs;
        if (!latlngs.length) return shape;
        else if (isFlat(latlngs) && this.isInLatLngs(latlng, latlngs)) shape = latlngs;
        else if (isFlat(latlngs[0]) && this.isInLatLngs(latlng, latlngs[0])) shape = latlngs;
        else for (var i = 0; i < latlngs.length; i++) if (this.isInLatLngs(latlng, latlngs[i][0])) return latlngs[i];
        return shape;
    },

    isInLatLngs: function (l, latlngs) {
        var inside = false, l1, l2, j, k, len2;

        for (j = 0, len2 = latlngs.length, k = len2 - 1; j < len2; k = j++) {
            l1 = latlngs[j];
            l2 = latlngs[k];

            if (((l1.lat > l.lat) !== (l2.lat > l.lat)) &&
                (l.lng < (l2.lng - l1.lng) * (l.lat - l1.lat) / (l2.lat - l1.lat) + l1.lng)) {
                inside = !inside;
            }
        }

        return inside;
    },

    parentShape: function (shape, latlngs) {
        latlngs = latlngs || this._latlngs;
        if (!latlngs) return;
        var idx = L.Util.indexOf(latlngs, shape);
        if (idx !== -1) return latlngs;
        for (var i = 0; i < latlngs.length; i++) {
            idx = L.Util.indexOf(latlngs[i], shape);
            if (idx !== -1) return latlngs[i];
        }
    }

};

if (L.Polygon) {
    L.Polygon.include(EditableMixin);
    L.Polygon.include(PolygonMixin);
}

export default PolygonEditor;
