import VersionedOGC from '../Format.XML.VersionedOGC';
import L from "leaflet";

export let WMTSCapabilities =  VersionedOGC.extend({
    name: "WMTSCapabilities",
    defaultVersion: "1.0.0",
    yx: {
        "urn:ogc:def:crs:EPSG::4326": true,
        "urn:ogc:def:crs:EPSG::4490": true,
        "urn:ogc:def:crs:EPSG::900913": true,
        "urn:ogc:def:crs:EPSG::4610": true
    },
    xy: {
        "urn:ogc:def:crs:EPSG::3857": true,
        "urn:ogc:def:crs:EPSG::3785": true,
        "urn:ogc:def:crs:EPSG::102100": true
    }
});

L.geoway.format.WMTSCapabilities = WMTSCapabilities;
L.Format = L.Format ? L.Format : {};
L.Format.WMTSCapabilities = WMTSCapabilities;

