import FormatXML from './Format.XML';
import '../core/Base';
import L from "leaflet";

let VersionedOGC = FormatXML.extend({
    defaultVersion: null,

    version: null,

    profile: null,

    allowFallback: false,

    name: null,

    stringifyOutput: false,

    parser: null,

    initialize: function(options) {
        FormatXML.prototype.initialize.apply(this, [options]);
    },

    getVersion: function(root, options) {
        var version;
        // read
        if (root) {
            version = this.version;
            if(!version) {
                version = root.getAttribute("version");
                if(!version) {
                    version = this.defaultVersion;
                }
            }
        } else { // write
            version = (options && options.version) ||
                this.version || this.defaultVersion;
        }
        return version;
    },

    getParser: function(version) {
        version = version || this.defaultVersion;
        if(!this.parser || this.parser.VERSION != version) {
            var format = L.geoway.format[this.name]["v" + version.replace(/\./g, "_")];
            if(format) {
                this.parser = new format(this.options);
            }
        }
        return this.parser;
    },

    /**
     * write document 对象
     * @method L.Format.XML.VersionedOGC#write
     * @param {Object} obj - document对象
     * @param {Object} options - 版本参数，默认不用写
     * @return {String} document - string to document
     */
    write: function(obj, options) {
        var version = this.getVersion(null, options);
        this.parser = this.getParser(version);
        var root = this.parser.write(obj, options);
        if (this.stringifyOutput === false) {
            return root;
        } else {
            return FormatXML.prototype.write.apply(this, [root]);
        }
    },

    /**
     * read string xml
     * @method L.Format.XML.VersionedOGC#read
     * @param {string} data - xml string
     * @param {Object} options - 版本参数，默认不用写
     * @return {Object} obj - string to document
     */
    read: function(data, options) {
        if(typeof data == "string") {
            data = FormatXML.prototype.read.apply(this, [data]);
        }
        var root = data.documentElement;
        var version = this.getVersion(root);
        this.parser = this.getParser(version);          // Select the parser
        var obj = this.parser.read(data, options);      // Parse the data

        obj.version = version;
        return obj;
    }
});

export default VersionedOGC;
