import L from 'leaflet';
import VertexMarker from './VertexMarker';
import MiddleMarker from './MiddleMarker';
import VertexIcon from './VertexIcon';
import TouchVertexIcon from './TouchVertexIcon';

let Editable = L.Evented.extend({

    statics: {
        FORWARD: 1,
        BACKWARD: -1
    },

    options: {

        // You can pass them when creating a map using the `editOptions` key.
        // 🍂option zIndex: int = 1000
        // The default zIndex of the editing tools.
        zIndex: 1000,

        // 🍂option polygonClass: class = L.Polygon
        // Class to be used when creating a new Polygon.
        polygonClass: L.Polygon,

        // 🍂option polylineClass: class = L.Polyline
        // Class to be used when creating a new Polyline.
        polylineClass: L.Polyline,

        // 🍂option markerClass: class = L.Marker
        // Class to be used when creating a new Marker.
        markerClass: L.Marker,

        // 🍂option rectangleClass: class = L.Rectangle
        // Class to be used when creating a new Rectangle.
        rectangleClass: L.Rectangle,

        // 🍂option circleClass: class = L.Circle
        // Class to be used when creating a new Circle.
        circleClass: L.Circle,

        // 🍂option drawingCSSClass: string = 'leaflet-editable-drawing'
        // CSS class to be added to the map container while drawing.
        drawingCSSClass: 'leaflet-editable-drawing',

        // 🍂option drawingCursor: const = 'crosshair'
        // Cursor mode set to the map while drawing.
        drawingCursor: 'crosshair',

        // 🍂option editLayer: Layer = new L.LayerGroup()
        // Layer used to store edit tools (vertex, line guide…).
        editLayer: undefined,

        // 🍂option featuresLayer: Layer = new L.LayerGroup()
        // Default layer used to store drawn features (Marker, Polyline…).
        featuresLayer: undefined,

        // 🍂option polylineEditorClass: class = PolylineEditor
        // Class to be used as Polyline editor.
        polylineEditorClass: undefined,

        // 🍂option polygonEditorClass: class = PolygonEditor
        // Class to be used as Polygon editor.
        polygonEditorClass: undefined,

        // 🍂option markerEditorClass: class = MarkerEditor
        // Class to be used as Marker editor.
        markerEditorClass: undefined,

        // 🍂option rectangleEditorClass: class = RectangleEditor
        // Class to be used as Rectangle editor.
        rectangleEditorClass: undefined,

        // 🍂option circleEditorClass: class = CircleEditor
        // Class to be used as Circle editor.
        circleEditorClass: undefined,

        // 🍂option lineGuideOptions: hash = {}
        // Options to be passed to the line guides.
        lineGuideOptions: {},

        // 🍂option skipMiddleMarkers: boolean = false
        // Set this to true if you don't want middle markers.
        skipMiddleMarkers: false

    },

    initialize: function (map, options) {
        L.setOptions(this, options);
        this._lastZIndex = this.options.zIndex;
        this.map = map;
        this.editLayer = this.createEditLayer();
        this.featuresLayer = this.createFeaturesLayer();
        this.forwardLineGuide = this.createLineGuide();
        this.backwardLineGuide = this.createLineGuide();
    },

    fireAndForward: function (type, e) {
        e = e || {};
        e.editTools = this;
        this.fire(type, e);
        this.map.fire(type, e);
    },

    createLineGuide: function () {
        var options = L.extend({dashArray: '5,10', weight: 1, interactive: false}, this.options.lineGuideOptions);
        return L.polyline([], options);
    },

    createVertexIcon: function (options) {
        return L.Browser.mobile && L.Browser.touch ? new TouchVertexIcon(options) : new VertexIcon(options);
    },

    createEditLayer: function () {
        return this.options.editLayer || new L.LayerGroup().addTo(this.map);
    },

    createFeaturesLayer: function () {
        return this.options.featuresLayer || new L.LayerGroup().addTo(this.map);
    },

    moveForwardLineGuide: function (latlng) {
        if (this.forwardLineGuide._latlngs.length) {
            this.forwardLineGuide._latlngs[1] = latlng;
            this.forwardLineGuide._bounds.extend(latlng);
            this.forwardLineGuide.redraw();
        }
    },

    moveBackwardLineGuide: function (latlng) {
        if (this.backwardLineGuide._latlngs.length) {
            this.backwardLineGuide._latlngs[1] = latlng;
            this.backwardLineGuide._bounds.extend(latlng);
            this.backwardLineGuide.redraw();
        }
    },

    anchorForwardLineGuide: function (latlng) {
        this.forwardLineGuide._latlngs[0] = latlng;
        this.forwardLineGuide._bounds.extend(latlng);
        this.forwardLineGuide.redraw();
    },

    anchorBackwardLineGuide: function (latlng) {
        this.backwardLineGuide._latlngs[0] = latlng;
        this.backwardLineGuide._bounds.extend(latlng);
        this.backwardLineGuide.redraw();
    },

    attachForwardLineGuide: function () {
        this.editLayer.addLayer(this.forwardLineGuide);
    },

    attachBackwardLineGuide: function () {
        this.editLayer.addLayer(this.backwardLineGuide);
    },

    detachForwardLineGuide: function () {
        this.forwardLineGuide.setLatLngs([]);
        this.editLayer.removeLayer(this.forwardLineGuide);
    },

    detachBackwardLineGuide: function () {
        this.backwardLineGuide.setLatLngs([]);
        this.editLayer.removeLayer(this.backwardLineGuide);
    },

    blockEvents: function () {
        // Hack: force map not to listen to other layers events while drawing.
        if (!this._oldTargets) {
            this._oldTargets = this.map._targets;
            this.map._targets = {};
        }
    },

    unblockEvents: function () {
        if (this._oldTargets) {
            // Reset, but keep targets created while drawing.
            this.map._targets = L.extend(this.map._targets, this._oldTargets);
            delete this._oldTargets;
        }
    },

    registerForDrawing: function (editor) {
        if (this._drawingEditor) this.unregisterForDrawing(this._drawingEditor);
        this.blockEvents();
        editor.reset();  // Make sure editor tools still receive events.
        this._drawingEditor = editor;
        this.map.on('mousemove touchmove', editor.onDrawingMouseMove, editor);
        this.map.on('mousedown', this.onMousedown, this);
        this.map.on('mouseup', this.onMouseup, this);
        L.DomUtil.addClass(this.map._container, this.options.drawingCSSClass);
        this.defaultMapCursor = this.map._container.style.cursor;
        this.map._container.style.cursor = this.options.drawingCursor;
    },

    unregisterForDrawing: function (editor) {
        this.unblockEvents();
        L.DomUtil.removeClass(this.map._container, this.options.drawingCSSClass);
        this.map._container.style.cursor = this.defaultMapCursor;
        editor = editor || this._drawingEditor;
        if (!editor) return;
        this.map.off('mousemove touchmove', editor.onDrawingMouseMove, editor);
        this.map.off('mousedown', this.onMousedown, this);
        this.map.off('mouseup', this.onMouseup, this);
        if (editor !== this._drawingEditor) return;
        delete this._drawingEditor;
        if (editor._drawing) editor.cancelDrawing();
    },

    onMousedown: function (e) {
        if (e.originalEvent.which != 1) return;
        this._mouseDown = e;
        this._drawingEditor.onDrawingMouseDown(e);
    },

    onMouseup: function (e) {
        if (this._mouseDown) {
            var editor = this._drawingEditor,
                mouseDown = this._mouseDown;
            this._mouseDown = null;
            editor.onDrawingMouseUp(e);
            if (this._drawingEditor !== editor) return;  // onDrawingMouseUp may call unregisterFromDrawing.
            var origin = L.point(mouseDown.originalEvent.clientX, mouseDown.originalEvent.clientY);
            var distance = L.point(e.originalEvent.clientX, e.originalEvent.clientY).distanceTo(origin);
            if (Math.abs(distance) < 9 * (window.devicePixelRatio || 1)) this._drawingEditor.onDrawingClick(e);
        }
    },

    // 🍂section Public methods
    // You will generally access them by the `map.editTools`
    // instance:
    //
    // `map.editTools.startPolyline();`

    // 🍂method drawing(): boolean
    // Return true if any drawing action is ongoing.
    drawing: function () {
        return this._drawingEditor && this._drawingEditor.drawing();
    },

    // 🍂method stopDrawing()
    // When you need to stop any ongoing drawing, without needing to know which editor is active.
    stopDrawing: function () {
        this.unregisterForDrawing();
    },

    // 🍂method commitDrawing()
    // When you need to commit any ongoing drawing, without needing to know which editor is active.
    commitDrawing: function (e) {
        if (!this._drawingEditor) return;
        this._drawingEditor.commitDrawing(e);
    },

    connectCreatedToMap: function (layer) {
        return this.featuresLayer.addLayer(layer);
    },

    // 🍂method startPolyline(latlng: L.LatLng, options: hash): L.Polyline
    // Start drawing a Polyline. If `latlng` is given, a first point will be added. In any case, continuing on user click.
    // If `options` is given, it will be passed to the Polyline class constructor.
    startPolyline: function (latlng, options) {
        var line = this.createPolyline([], options);
        line.enableEdit(this.map).newShape(latlng);
        return line;
    },

    // 🍂method startPolygon(latlng: L.LatLng, options: hash): L.Polygon
    // Start drawing a Polygon. If `latlng` is given, a first point will be added. In any case, continuing on user click.
    // If `options` is given, it will be passed to the Polygon class constructor.
    startPolygon: function (latlng, options) {
        var polygon = this.createPolygon([], options);
        polygon.enableEdit(this.map).newShape(latlng);
        return polygon;
    },

    // 🍂method startMarker(latlng: L.LatLng, options: hash): L.Marker
    // Start adding a Marker. If `latlng` is given, the Marker will be shown first at this point.
    // In any case, it will follow the user mouse, and will have a final `latlng` on next click (or touch).
    // If `options` is given, it will be passed to the Marker class constructor.
    startMarker: function (latlng, options) {
        latlng = latlng || this.map.getCenter().clone();
        var marker = this.createMarker(latlng, options);
        marker.enableEdit(this.map).startDrawing();
        return marker;
    },

    // 🍂method startRectangle(latlng: L.LatLng, options: hash): L.Rectangle
    // Start drawing a Rectangle. If `latlng` is given, the Rectangle anchor will be added. In any case, continuing on user drag.
    // If `options` is given, it will be passed to the Rectangle class constructor.
    startRectangle: function(latlng, options) {
        var corner = latlng || L.latLng([0, 0]);
        var bounds = new L.LatLngBounds(corner, corner);
        var rectangle = this.createRectangle(bounds, options);
        rectangle.enableEdit(this.map).startDrawing();
        return rectangle;
    },

    // 🍂method startCircle(latlng: L.LatLng, options: hash): L.Circle
    // Start drawing a Circle. If `latlng` is given, the Circle anchor will be added. In any case, continuing on user drag.
    // If `options` is given, it will be passed to the Circle class constructor.
    startCircle: function (latlng, options) {
        latlng = latlng || this.map.getCenter().clone();
        var circle = this.createCircle(latlng, options);
        circle.enableEdit(this.map).startDrawing();
        return circle;
    },

    startHole: function (editor, latlng) {
        editor.newHole(latlng);
    },

    createLayer: function (klass, latlngs, options) {
        options = L.Util.extend({editOptions: {editTools: this}}, options);
        var layer = new klass(latlngs, options);
        // 🍂namespace Editable
        // 🍂event editable:created: LayerEvent
        // Fired when a new feature (Marker, Polyline…) is created.
        this.fireAndForward('editable:created', {layer: layer});
        return layer;
    },

    createPolyline: function (latlngs, options) {
        return this.createLayer(options && options.polylineClass || this.options.polylineClass, latlngs, options);
    },

    createPolygon: function (latlngs, options) {
        return this.createLayer(options && options.polygonClass || this.options.polygonClass, latlngs, options);
    },

    createMarker: function (latlng, options) {
        return this.createLayer(options && options.markerClass || this.options.markerClass, latlng, options);
    },

    createRectangle: function (bounds, options) {
        return this.createLayer(options && options.rectangleClass || this.options.rectangleClass, bounds, options);
    },

    createCircle: function (latlng, options) {
        return this.createLayer(options && options.circleClass || this.options.circleClass, latlng, options);
    }

});

L.extend(Editable, {

    makeCancellable: function (e) {
        e.cancel = function () {
            e._cancelled = true;
        };
    }

});

L.Map.mergeOptions({

    // 🍂namespace Map
    // 🍂section Map Options
    // 🍂option editToolsClass: class = L.Editable
    // Class to be used as vertex, for path editing.
    editToolsClass: Editable,

    // 🍂option editable: boolean = false
    // Whether to create a L.Editable instance at map init.
    editable: false,

    // 🍂option editOptions: hash = {}
    // Options to pass to L.Editable when instantiating.
    editOptions: {}

});

L.Map.addInitHook(function () {

    this.whenReady(function () {
        if (this.options.editable) {
            this.editTools = new this.options.editToolsClass(this, this.options.editOptions);
        }
    });

});

Editable.mergeOptions({
    // Class to be used as vertex, for path editing.
    vertexMarkerClass: VertexMarker,
    // Class to be used as middle vertex, pulled by the user to create a new point in the middle of a path.
    middleMarkerClass: MiddleMarker
});

L.LatLng.prototype.update = function (latlng) {
    latlng = L.latLng(latlng);
    this.lat = latlng.lat;
    this.lng = latlng.lng;
}

var isFlat = L.LineUtil.isFlat || L.LineUtil._flat || L.Polyline._flat;  // <=> 1.1 compat.

var EditableMixin = {

    createEditor: function (map) {
        map = map || this._map;
        var tools = (this.options.editOptions || {}).editTools || map.editTools;
        if (!tools) throw Error('Unable to detect Editable instance.');
        var Klass = this.options.editorClass || this.getEditorClass(tools);
        return new Klass(map, this, this.options.editOptions);
    },

    // 🍂method enableEdit(map?: L.Map): this.editor
    // Enable editing, by creating an editor if not existing, and then calling `enable` on it.
    enableEdit: function (map) {
        if (!this.editor) this.createEditor(map);
        this.editor.enable();
        return this.editor;
    },

    // 🍂method editEnabled(): boolean
    // Return true if current instance has an editor attached, and this editor is enabled.
    editEnabled: function () {
        return this.editor && this.editor.enabled();
    },

    // 🍂method disableEdit()
    // Disable editing, also remove the editor property reference.
    disableEdit: function () {
        if (this.editor) {
            this.editor.disable();
            delete this.editor;
        }
    },

    // 🍂method toggleEdit()
    // Enable or disable editing, according to current status.
    toggleEdit: function () {
        if (this.editEnabled()) this.disableEdit();
        else this.enableEdit();
    },

    _onEditableAdd: function () {
        if (this.editor) this.enableEdit();
    }

};

var keepEditable = function () {
    // Make sure you can remove/readd an editable layer.
    this.on('add', this._onEditableAdd);
};

export default Editable;

export {
    isFlat,
    EditableMixin,
    keepEditable
}
