import L from 'leaflet';
import '../../core/Base';
import Wkt from "../../format/layer/Wkt";

let FeatureAttr = L.Class.extend({
    featureService: null,
    fields: null,
    options: {
        isEdit: false,//属性框用途，新增要素或编辑要素
        layerId: null, //数据集id
        feature: null, //当前编辑或添加要素对象
        fId: null, //要素主键id
        position:{
            top: 10,
            right: 10
        },
        width: 360,
        height: 310
    },

    initialize: function(featureService, options){
        this.featureService = featureService;
        L.setOptions(this, options);
        this.getFields(options.layerId);
    },

    getFields: function(layerId){
        let that = this;
        this.featureService.infoById(layerId,function (res) {
            that.fields = res.layer.fields;
        },function (e) {
            console.log(e);
        });
    },

    findFieldType: function(field){
        let obj = this.fields.find(item => item.name === field);
        if(obj){
            return obj.type;
        }else {
            return null;
        }
    },

    render: function (attributes) {
        if(this.options.feature.renderAttr){
            return false;
        }
        let options = this.options;
        let style = `height: ${options.height}px; width: ${options.width}px;`;
        for(let key in options.position){
            style += `${key}: ${options.position[key]}px;`;
        }
        let htmlTemplate = `<div class="attr-panel" style="${style}"><h2>属性信息<i></i></h2><div class="attr-con"><div class="attr-main"><ul>`;
        for(let field in attributes){
            let fieldValue = attributes[field] || "";
            htmlTemplate += `<li><span style="width:40%;">${field}</span><span style="width:60%;"><input type="text" value="${fieldValue}"/></span></li>`;
        }
        htmlTemplate += `</ul></div><div class="attr-btn"><button type="button">保存</button><button type="button">删除</button></div></div>`;
        let container = L.DomUtil.create('div', 'attr-panel-wrap');
        container.innerHTML = htmlTemplate;
        let bodyEl = document.getElementsByTagName('body')[0];
        bodyEl.appendChild(container);
        //绑定弹框关闭事件
        L.DomEvent.on(document.querySelector('.attr-panel i'),'click', this.close, this);
        //绑定保存事件
        L.DomEvent.on(document.querySelector('.attr-btn :nth-child(1)'),'click', this.saveHandle, this);
        //绑定删除事件
        L.DomEvent.on(document.querySelector('.attr-btn :nth-child(2)'),'click',this.delete, this);
        this.options.feature.renderAttr = true;
    },

    close: function () {
        if(this.options.feature.editor){
            this.options.feature.disableEdit();
        }
        let bodyEl = document.getElementsByTagName('body')[0];
        let container = document.getElementsByClassName('attr-panel-wrap')[0];
        bodyEl.removeChild(container);
        this.options.feature.renderAttr = false;
    },
    saveHandle: function () {
        let list = document.getElementsByTagName('li');
        let attributes = {};
        for(let i=0;i<list.length; i++){
            let child = list[i].childNodes;
            let field = child[0].innerText;
            let fieldType = this.findFieldType(field);
            let value = child[1].firstChild.value;
            if(["int", "short", "long"].includes(fieldType)){
                attributes[field] = parseInt(value);
            }else if(["float", "double"].includes(fieldType)){
                attributes[field] = parseFloat(value);
            }else{
                attributes[field] = value;
            }
        }
        let data = {};
        data.attributes  = attributes;
        var wkt = new Wkt.Wkt();
        wkt.fromObject(this.options.feature);
        data.geometry = wkt.write();
        if(this.options.isEdit){
            data.fId = this.options.fId;
        }
        this.save([data]);
    },
    delete: function () {
        let that = this;
        let options = this.options;
        if(!options.isEdit){
            this.featureService.featureGroup.removeLayer(options.feature);
            that.close();
        }else{
            this.featureService.deleteFeatures(options.layerId, options.fId, function (res) {
                that.featureService.fire("feature:delete", res);
                that.close();
            },function(e){
                console.error(e);
            });
        }
    },

    save: function (data) {
        let that = this;
        let options = this.options;
        if(!options.isEdit){
            this.featureService.addFeatures(options.layerId, data, function (res) {
                that.featureService.fire("feature:save", res);
                that.close();
            },function(e){
                console.error(e);
            });
        }else{
            this.featureService.updateFeatures(options.layerId, data, function (res) {
                that.featureService.fire("feature:save", res);
                that.close();
            },function(e){
                console.error(e);
            });
        }
    }
});

export default FeatureAttr;
