import L from 'leaflet';
import '../../core/Base';
import SimpleShape from './SimpleShape';
import TextMessage from './TextMessage';
import '../../util/Util';

export let DrawCircle = SimpleShape.extend({
    statics: {
        TYPE: 'circle'
    },
    options: {
        shapeOptions: {
            stroke: true,
            color: '#f06eaa',
            weight: 4,
            opacity: 0.5,
            fill: true,
            fillColor: null, //same as color by default
            fillOpacity: 0.2,
            clickable: true
        },
        showRadius: true,
        metric: true, // Whether to use the metric measurement system or imperial
        feet: true // When not metric, use feet instead of yards for display
    },

    initialize: function (map, options) {
        // Save the type so super can fire, need to do this as cannot do this.TYPE :(
        this.type = DrawCircle.TYPE;

        this._initialLabelText = TextMessage.draw.handlers.circle.tooltip.start;

        SimpleShape.prototype.initialize.call(this, map, options);
    },

    _drawShape: function (latlng) {
        if (!this._shape) {
            this._shape = new L.Circle(this._startLatLng, this._startLatLng.distanceTo(latlng), this.options.shapeOptions);
            this._map.addLayer(this._shape);
        } else {
            this._shape.setRadius(this._startLatLng.distanceTo(latlng));
        }
    },

    _fireCreatedEvent: function () {
        var circle = new L.Circle(this._startLatLng, this._shape.getRadius(), this.options.shapeOptions);
        SimpleShape.prototype._fireCreatedEvent.call(this, circle);
    },

    _onMouseMove: function (e) {
        var latlng = e.latlng,
            showRadius = this.options.showRadius,
            useMetric = this.options.metric,
            radius;

        this._tooltip.updatePosition(latlng);
        if (this._isDrawing) {
            this._drawShape(latlng);

            // Get the new radius (rounded to 1 dp)
            radius = this._shape.getRadius().toFixed(1);

            this._tooltip.updateContent({
                text: this._endLabelText,
                subtext: showRadius ? TextMessage.draw.handlers.circle.radius + ': ' +
                    L.Util.readableDistance(radius, useMetric, this.options.feet) : ''
            });
        }
    }
});

export let drawCircle =  function (map, options) {
    return new DrawCircle(map, options);
}

L.geoway.control.drawCircle = drawCircle;
L.geoway.control.DrawCircle = DrawCircle;
L.Draw = L.Draw ? L.Draw : {};
L.Draw.Circle = DrawCircle;
