import L from 'leaflet';
import '../core/Base';
import {DrawPolygon} from "./draw/DrawPolygon";
import TipPopup from '../util/TipPopup';
import '../util/Util';

export let MeasureArea = DrawPolygon.extend({
    options: {
        showArea: true,
        shapeOptions: {
            stroke: true,
            color: '#f06eaa',
            weight: 4,
            opacity: 0.5,
            fill: true,
            fillColor: null, //same as color by default
            fillOpacity: 0.2,
            clickable: true
        },
        metric: true // Whether to use the metric measurement system or imperial
    },
    initialize: function (map, options) {
        DrawPolygon.prototype.initialize.call(this, map, options);
        this._cache = [];
    },
    addHooks: function () {
        DrawPolygon.prototype.addHooks.call(this);
        if (this._map && !this._areaGroup) {
            this._areaGroup = new L.LayerGroup();
            this._map.addLayer(this._areaGroup);
        }
    },

    removeHooks: function () {
        this._clearHideErrorTimeout();

        this._cleanUpShape();

        // remove markers from map
        this._map.removeLayer(this._markerGroup);
        delete this._markerGroup;
        delete this._markers;

        this._map.removeLayer(this._poly);
        delete this._poly;

        this._mouseMarker
            .off('mousedown', this._onMouseDown, this)
            .off('mouseout', this._onMouseOut, this)
            .off('mouseup', this._onMouseUp, this)
            .off('mousemove', this._onMouseMove, this);
        this._map.removeLayer(this._mouseMarker);
        delete this._mouseMarker;

        // clean up DOM
        this._clearGuides();

        this._map
            .off('mouseup', this._onMouseUp, this)
            .off('mousemove', this._onMouseMove, this)
            .off('zoomlevelschange', this._onZoomEnd, this)
            .off('zoomend', this._onZoomEnd, this)
            .off('click', this._onTouch, this);
        this._container.style.cursor = '';
        this._tooltip.dispose();
    },

    _finishShape: function () {
        this._cleanUpShape();
        this._clearGuides();

        var latlngs = this._poly.getLatLngs();
        var lastLatlng = latlngs[latlngs.length - 1];
        var intersects = this._poly.newLatLngIntersects(lastLatlng);
        if ((!this.options.allowIntersection && intersects) || !this._shapeIsValid()) {
            this._showErrorTooltip();
            return;
        }

        var area = L.Util.geodesicArea(latlngs);
        area = L.Util.readableArea(area, this.options.metric)

        var poly = new this.Poly(this._poly.getLatLngs(), this.options.shapeOptions);
        this._areaGroup.addLayer(poly);
        var polyId = L.stamp(poly);
        var measureId = "measure-" + polyId;

        var popup = new TipPopup({offset : new L.Point(36,-4)});
        popup.setLatLng(lastLatlng).setContent(area).openOn(this._map);
        popup._sort = "tip-popup";

        //var content = popup.getContent();
        var closeBtn = L.DomUtil.create('div', "leaflet-measure-tipclose");
        closeBtn.id = measureId;
        closeBtn.title = '清除本次测面';
        popup._contentNode.appendChild(closeBtn);
        var w = popup._contentNode.offsetWidth;
        popup._contentNode.style.width = (w + 30) + "px";
        L.DomEvent.on(closeBtn, 'click', this._onTipClose, this);

        var cache = {
            id : measureId,
            popup : popup,
            poly : poly
        };
        this._cache.push(cache);

        this.disable();

    },
    _onTipClose : function(e){
        var el = e.target || e.srcElement;
        var measureId = el.id;
        var cache = this._getCacheById(measureId);
        if(cache){
            var popup = cache.popup;
            var poly = cache.poly;
            this._map.removeLayer(poly);
            this._map.removeLayer(popup);
            this._deleteCacheById(measureId);
        }
    },
    _getCacheById : function(id){
        var cache;
        for(var i = 0;i < this._cache.length;i++){
            if(this._cache[i].id == id){
                cache = this._cache[i];
                break;
            }
        }
        return cache;
    },
    _deleteCacheById : function(id){
        for(var i = this._cache.length-1;i >=0;i--){
            if(this._cache[i].id == id){
                this._cache.splice(i,1);
                break;
            }
        }
    },
    clear : function(){
        var caches = this._cache;
        for(var k=0;k<caches.length;k++){
            var cache = caches[k];
            var popup = cache.popup;
            var poly = cache.poly;
            this._map.removeLayer(poly);
            this._map.removeLayer(popup);
        }
        this._cache = [];
    }
});

export let measureArea = function (map, options) {
    return new MeasureArea(map, options);
};

L.geoway.control.measureArea = measureArea;
L.geoway.control.MeasureArea = MeasureArea;
L.Measure = L.Measure ? L.Measure : {};
L.Measure.Area = MeasureArea;
