import L from 'leaflet';
import '../../core/Base';
import FetchRequest from '../../util/FetchRequest';

export let GeocoderService = L.Class.extend({
    options: {
        url: null
    },
    initialize: function(options){
        L.setOptions(this, options);
    },
    /**
     * 正地理编码查询
     * @method L.Geocoder#getLatLngInfo
     * @param {object}   param - 查询参数address、city、format属性
     * @param {Function} callback - 回调函数
     */
    getLatLngInfo: function(param,callback){
        this._getLatLngInfo(param,callback);
    },
    /**
     * 逆地理编码查询
     * @method L.Geocoder#getLocation
     * @param {L.LatLng} latlng   - 坐标位置
     * @param {Function} callback - 回调函数
     */
    getLocation: function(latlng,callback){
        this._getLocation(latlng,callback);
    },
    _getLatLngInfo: function(param,callback){
        var url = this.options.url;
        if(!url) {return false;}
        var address = param.address;
        var city = param.city;
        var format = param.format || "json";
        url = url + "/geo?format=" + format +"&address=" + address;
        if(city){
            url = url + "&city=" + city;
        }
        FetchRequest.get(url).then(function(result){
            if(callback && typeof(callback) == "function"){
                callback(result);
            }
        });
    },
    _getLocation: function(latlng,callback){
        var url = this.options.url;
        if(!url) {return false;}
        url = url + "/rgeo?format=json&lon=" + latlng.lng + "&lat=" + latlng.lat;
        FetchRequest.get(url).then(function(result){
            if(callback && typeof(callback) == "function"){
                callback(result);
            }
        });
    }
});

export let geocoderService = function (options) {
    return new GeocoderService(options);
}

L.geoway.service.geocoderService = geocoderService;
L.geoway.service.GeocoderService = GeocoderService;
L.Geocoder = GeocoderService;
