import L from 'leaflet';
import '../core/Base';

export let SingleImageLayer = L.Layer.extend({
    options: {
        opacity: 1,
        zIndex: 1,
        pane: 'tilePane'
    },
    _params: null,

    initialize: function (url, params, options) {
        this.url = url;
        this._params = params;
        L.Util.setOptions(this, options);
    },

    onAdd: function (map) {
        map.on('moveend', this._update, this);

        // if we had an image loaded and it matches the
        // current bounds show the image otherwise remove it
        if (this._currentImage && this._currentImage._bounds.equals(this._map.getBounds())) {
            map.addLayer(this._currentImage);
        } else if (this._currentImage) {
            this._map.removeLayer(this._currentImage);
            this._currentImage = null;
        }

        this._update();
    },

    onRemove: function () {
        if (this._currentImage) {
            this._map.removeLayer(this._currentImage);
        }

        this._map.off('moveend', this._update, this);
    },

    getOpacity: function () {
        return this.options.opacity;
    },

    setOpacity: function (opacity) {
        this.options.opacity = opacity;
        if (this._currentImage) {
            this._currentImage.setOpacity(opacity);
        }
        return this;
    },

    getZIndex: function(){
        return this.options.zIndex;
    },

    setZIndex: function(zIndex){
        this.options.zIndex = zIndex;
        if (this._currentImage) {
            this._currentImage.setZIndex(zIndex);
        }
        return this;
    },

    redraw: function () {
        this._update();
    },

    _renderImage: function (url, bounds, contentType) {
        if (this._map) {
            // if no output directory has been specified for a service, MIME data will be returned
            if (contentType) {
                url = 'data:' + contentType + ';base64,' + url;
            }
            // create a new image overlay and add it to the map
            // to start loading the image
            // opacity is 0 while the image is loading
            var image = new L.ImageOverlay(url, bounds, {
                opacity: this.options.opacity,
                crossOrigin: this.options.useCors,
                pane: this.options.pane || this.getPane(),
                interactive: this.options.interactive,
                zIndex: this.options.zIndex
            }).addTo(this._map);

            // once the image loads
            image.once('load', function (e) {
                if (this._map) {
                    var newImage = e.target;
                    var oldImage = this._currentImage;

                    // if the bounds of this image matches the bounds that
                    // _renderImage was called with and we have a map with the same bounds
                    // hide the old image if there is one and set the opacity
                    // of the new image otherwise remove the new image
                    if (newImage._bounds.equals(bounds) && newImage._bounds.equals(this._map.getBounds())) {
                        this._currentImage = newImage;

                        if (this._map && this._currentImage._map) {
                            this._currentImage.setOpacity(this.options.opacity);
                            this._currentImage.setZIndex(this.options.zIndex);
                        } else {
                            this._currentImage._map.removeLayer(this._currentImage);
                        }

                        if (oldImage && this._map) {
                            this._map.removeLayer(oldImage);
                        }

                        if (oldImage && oldImage._map) {
                            oldImage._map.removeLayer(oldImage);
                        }
                    } else {
                        this._map.removeLayer(newImage);
                    }
                }

                this.fire('load', {
                    bounds: bounds
                });
            }, this);

            this.fire('loading', {
                bounds: bounds
            });
        }
    },

    _buildExportParams: function () {
        var bounds = this._map.getBounds();
        var size = this._map.getSize();
        var ne = this._map.options.crs.project(bounds._northEast);
        var sw = this._map.options.crs.project(bounds._southWest);

        var top = this._map.latLngToLayerPoint(bounds._northEast);
        var bottom = this._map.latLngToLayerPoint(bounds._southWest);

        if (top.y > 0 || bottom.y < size.y) {
            size.y = bottom.y - top.y;
        }

        var params = {
            xmin: sw.x,
            ymin: sw.y,
            xmax: ne.x,
            ymax: ne.y,
            width: size.x,
            height: size.y
        };
        return params;
    },

    _update: function () {
        if (!this._map) {
            return;
        }

        var zoom = this._map.getZoom();
        var bounds = this._map.getBounds();

        if (this._animatingZoom) {
            return;
        }

        if (this._map._panTransition && this._map._panTransition._inProgress) {
            return;
        }

        if (zoom > this.options.maxZoom || zoom < this.options.minZoom) {
            if (this._currentImage) {
                this._currentImage._map.removeLayer(this._currentImage);
                this._currentImage = null;
            }
            return;
        }

        var params = this._buildExportParams();

        this._requestExport(params, bounds);
    },

    _requestExport: function (params, bounds) {
        var url = L.Util.template(this.url, L.Util.extend(params));
        if(this._params){
            url += L.Util.getParamString(this._params, url);
        }
        this._renderImage(url, bounds);
    }

});

export let singleImageLayer = function (url, options) {
    return new SingleImageLayer(url, options);
};

L.geoway.singleImageLayer = singleImageLayer;
L.geoway.SingleImageLayer = SingleImageLayer;
