import L from 'leaflet';

L.extend(L.LatLng, {
    DEG_TO_RAD: Math.PI / 180,
    RAD_TO_DEG: 180 / Math.PI,
    MAX_MARGIN: 1.0E-9
});

L.extend(L.Util, {
    proxyUtil: function( fn, context ) {
        var args, proxy, tmp;
        if ( typeof context === "string" ) {
            tmp = fn[ context ];
            context = fn;
            fn = tmp;
        }
        if ( !(typeof fn == 'function')) {
            return undefined;
        }
        args = Array.prototype.slice.call( arguments, 2 );
        proxy = function() {
            return fn.apply( context || this, args.concat( Array.prototype.slice.call( arguments ) ) );
        };
        return proxy;
    },
    writeGeometryToWKT: function(geometry,type){
        var wkt;
        if(type.toLowerCase() == "point"){
            wkt = 'POINT(' + geometry.lng + ' ' + geometry.lat +')';
        }else if(type.toLowerCase() == "line"){
            wkt = 'LINESTRING(';
            var linePoints = [];
            for(var m=0;m<geometry.length;m++){
                linePoints.push(geometry[m].lng + " " + geometry[m].lat);
            }
            wkt += linePoints.join(",");
            wkt += ')';
        }else if(type.toLowerCase() == "polygon"){
            wkt = 'POLYGON(';
            var polygons = [];
            for(var i=0;i<geometry.length;i++){
                var points = [];
                for(var j=0;j<geometry[i].length;j++){
                    points.push(geometry[i][j].lng + " " + geometry[i][j].lat);

                    if(i==geometry.length-1 && j==geometry[i].length-1){
                        points.push(geometry[0][0].lng + " " + geometry[0][0].lat);
                    }
                }
                polygons.push("(" +points.join(",")+ ")");
            }
            wkt += polygons.join(",");
            wkt += ')';
        }
        return wkt;
    },
    getParam: function(param) {
        var local = document.location.search.substring(1);
        var splits = local.split("&");
        for (var i = 0; i < splits.length; i++) {
            var sp = splits[i];
            if (sp.indexOf(param + "=") == 0) {
                var val = sp.substring(param.length + 1);
                return decodeURIComponent(val);
            }
        }
    },
    geodesicArea: function (latLngs) {
        var pointsCount = latLngs.length,
            area = 0.0,
            d2r = L.LatLng.DEG_TO_RAD,
            p1, p2;

        if (pointsCount > 2) {
            for (var i = 0; i < pointsCount; i++) {
                p1 = latLngs[i];
                p2 = latLngs[(i + 1) % pointsCount];
                area += ((p2.lng - p1.lng) * d2r) *
                    (2 + Math.sin(p1.lat * d2r) + Math.sin(p2.lat * d2r));
            }
            area = area * 6378137.0 * 6378137.0 / 2.0;
        }

        return Math.abs(area);
    },
    readableArea: function (area, isMetric) {
        var areaStr;

        if (isMetric) {
            if (area >= 1000000) {
                areaStr = (area * 0.000001).toFixed(2) + ' 平方公里';
            } else {
                areaStr = area.toFixed(2) + ' 平方米;';
            }
        } else {
            area /= 0.836127; // Square yards in 1 meter

            if (area >= 3097600) { //3097600 square yards in 1 square mile
                areaStr = (area / 3097600).toFixed(2) + ' mi&sup2;';
            } else if (area >= 4840) {//48040 square yards in 1 acre
                areaStr = (area / 4840).toFixed(2) + ' acres';
            } else {
                areaStr = Math.ceil(area) + ' yd&sup2;';
            }
        }

        return areaStr;
    },
    readableDistance: function (distance, isMetric, useFeet) {
        var distanceStr;

        if (isMetric) {
            // show metres when distance is < 1km, then show km
            if (distance > 1000) {
                distanceStr = (distance  / 1000).toFixed(2) + ' 千米';
            } else {
                distanceStr = Math.ceil(distance) + ' 米';
            }
        } else {
            distance *= 1.09361;

            if (distance > 1760) {
                distanceStr = (distance / 1760).toFixed(2) + ' miles';
            } else {
                var suffix = ' yd';
                if (useFeet) {
                    distance = distance * 3;
                    suffix = ' ft';
                }
                distanceStr = Math.ceil(distance) + suffix;
            }
        }

        return distanceStr;
    },
    tryFunc : function(){
        var returnValue = null;
        for (var i=0, len=arguments.length; i<len; i++) {
            var lambda = arguments[i];
            try {
                returnValue = lambda();
                break;
            } catch (e) {
                //console.log(e);
            }
        }
        return returnValue;
    },
    bind: function(func, object) {
        // create a reference to all arguments past the second one
        var args = Array.prototype.slice.apply(arguments, [2]);
        return function() {
            // Push on any additional arguments from the actual function call.
            // These will come after those sent to the bind call.
            var newArgs = args.concat(
                Array.prototype.slice.apply(arguments, [0])
            );
            return func.apply(object, newArgs);
        };
    },
    applyDefaults : function(to, from){
        to = to || {};
        /*
         * FF/Windows < 2.0.0.13 reports "Illegal operation on WrappedNative
         * prototype object" when calling hawOwnProperty if the source object is an
         * instance of window.Event.
         */
        var fromIsEvt = typeof window.Event == "function"
            && from instanceof window.Event;

        for (var key in from) {
            if (to[key] === undefined ||
                (!fromIsEvt && from.hasOwnProperty
                    && from.hasOwnProperty(key) && !to.hasOwnProperty(key))) {
                to[key] = from[key];
            }
        }
        /**
         * IE doesn't include the toString property when iterating over an object's
         * properties with the for(property in object) syntax.  Explicitly check if
         * the source has its own toString property.
         */
        if(!fromIsEvt && from && from.hasOwnProperty
            && from.hasOwnProperty('toString') && !to.hasOwnProperty('toString')) {
            to.toString = from.toString;
        }

        return to;
    },
    cloneLatLngs: function (latlngs) {
        var clone = [];
        for (var i = 0, l = latlngs.length; i < l; i++) {
            // Check for nested array (Polyline/Polygon)
            if (Array.isArray(latlngs[i])) {
                clone.push(L.Util.cloneLatLngs(latlngs[i]));
            } else {
                clone.push(this.cloneLatLng(latlngs[i]));
            }
        }
        return clone;
    },
    cloneLatLng: function (latlng) {
        return L.latLng(latlng.lat, latlng.lng);
    },
    meterToDegree: function(meter){
        return meter / (2 * Math.PI * 6371004 ) *360;
    },
    degreeToMeter: function (degree) {
        return (2 * Math.PI * 6371004) * 360 * degree;
    }
});

L.Polyline.include({
    // Check to see if this polyline has any linesegments that intersect.
    // NOTE: does not support detecting intersection for degenerate cases.
    intersects: function () {
        var points = this._originalPoints,
            len = points ? points.length : 0,
            i, p, p1;

        if (this._tooFewPointsForIntersection()) {
            return false;
        }

        for (i = len - 1; i >= 3; i--) {
            p = points[i - 1];
            p1 = points[i];


            if (this._lineSegmentsIntersectsRange(p, p1, i - 2)) {
                return true;
            }
        }

        return false;
    },

    // Check for intersection if new latlng was added to this polyline.
    // NOTE: does not support detecting intersection for degenerate cases.
    newLatLngIntersects: function (latlng, skipFirst) {
        // Cannot check a polyline for intersecting lats/lngs when not added to the map
        if (!this._map) {
            return false;
        }

        return this.newPointIntersects(this._map.latLngToLayerPoint(latlng), skipFirst);
    },

    // Check for intersection if new point was added to this polyline.
    // newPoint must be a layer point.
    // NOTE: does not support detecting intersection for degenerate cases.
    newPointIntersects: function (newPoint, skipFirst) {
        var points = this._originalPoints,
            len = points ? points.length : 0,
            lastPoint = points ? points[len - 1] : null,
            // The previous previous line segment. Previous line segment doesn't need testing.
            maxIndex = len - 2;

        if (this._tooFewPointsForIntersection(1)) {
            return false;
        }

        return this._lineSegmentsIntersectsRange(lastPoint, newPoint, maxIndex, skipFirst ? 1 : 0);
    },

    // Polylines with 2 sides can only intersect in cases where points are collinear (we don't support detecting these).
    // Cannot have intersection when < 3 line segments (< 4 points)
    _tooFewPointsForIntersection: function (extraPoints) {
        var points = this._originalPoints,
            len = points ? points.length : 0;
        // Increment length by extraPoints if present
        len += extraPoints || 0;

        return !this._originalPoints || len <= 3;
    },

    // Checks a line segment intersections with any line segments before its predecessor.
    // Don't need to check the predecessor as will never intersect.
    _lineSegmentsIntersectsRange: function (p, p1, maxIndex, minIndex) {
        var points = this._originalPoints,
            p2, p3;

        minIndex = minIndex || 0;

        // Check all previous line segments (beside the immediately previous) for intersections
        for (var j = maxIndex; j > minIndex; j--) {
            p2 = points[j - 1];
            p3 = points[j];

            if (L.LineUtil.segmentsIntersect(p, p1, p2, p3)) {
                return true;
            }
        }

        return false;
    }
});


L.Polygon.include({
    // Checks a polygon for any intersecting line segments. Ignores holes.
    intersects: function () {
        var polylineIntersects,
            points = this._originalPoints,
            len, firstPoint, lastPoint, maxIndex;

        if (this._tooFewPointsForIntersection()) {
            return false;
        }

        polylineIntersects = L.Polyline.prototype.intersects.call(this);

        // If already found an intersection don't need to check for any more.
        if (polylineIntersects) {
            return true;
        }

        len = points.length;
        firstPoint = points[0];
        lastPoint = points[len - 1];
        maxIndex = len - 2;

        // Check the line segment between last and first point. Don't need to check the first line segment (minIndex = 1)
        return this._lineSegmentsIntersectsRange(lastPoint, firstPoint, maxIndex, 1);
    }
});
