import PathEditor from './PathEditor';
import Editable,{isFlat, EditableMixin, keepEditable}from './Editable';
import L from 'leaflet';

// 🍂namespace Editable; 🍂class PolylineEditor
// 🍂inherits PathEditor
let PolylineEditor = PathEditor.extend({

    startDrawingBackward: function () {
        this._drawing = Editable.BACKWARD;
        this.startDrawing();
    },

    // 🍂method continueBackward(latlngs?: Array)
    // Set up drawing tools to continue the line backward.
    continueBackward: function (latlngs) {
        if (this.drawing()) return;
        latlngs = latlngs || this.getDefaultLatLngs();
        this.setDrawnLatLngs(latlngs);
        if (latlngs.length > 0) {
            this.tools.attachBackwardLineGuide();
            this.tools.anchorBackwardLineGuide(latlngs[0]);
        }
        this.startDrawingBackward();
    },

    // 🍂method continueForward(latlngs?: Array)
    // Set up drawing tools to continue the line forward.
    continueForward: function (latlngs) {
        if (this.drawing()) return;
        latlngs = latlngs || this.getDefaultLatLngs();
        this.setDrawnLatLngs(latlngs);
        if (latlngs.length > 0) {
            this.tools.attachForwardLineGuide();
            this.tools.anchorForwardLineGuide(latlngs[latlngs.length - 1]);
        }
        this.startDrawingForward();
    },

    getDefaultLatLngs: function (latlngs) {
        latlngs = latlngs || this.feature._latlngs;
        if (!latlngs.length || latlngs[0] instanceof L.LatLng) return latlngs;
        else return this.getDefaultLatLngs(latlngs[0]);
    },

    ensureMulti: function () {
        if (this.feature._latlngs.length && isFlat(this.feature._latlngs)) {
            this.feature._latlngs = [this.feature._latlngs];
        }
    },

    addNewEmptyShape: function () {
        if (this.feature._latlngs.length) {
            var shape = [];
            this.appendShape(shape);
            return shape;
        } else {
            return this.feature._latlngs;
        }
    },

    formatShape: function (shape) {
        if (isFlat(shape)) return shape;
        else if (shape[0]) return this.formatShape(shape[0]);
    },

    // 🍂method splitShape(latlngs?: Array, index: int)
    // Split the given `latlngs` shape at index `index` and integrate new shape in instance `latlngs`.
    splitShape: function (shape, index) {
        if (!index || index >= shape.length - 1) return;
        this.ensureMulti();
        var shapeIndex = this.feature._latlngs.indexOf(shape);
        if (shapeIndex === -1) return;
        var first = shape.slice(0, index + 1),
            second = shape.slice(index);
        // We deal with reference, we don't want twice the same latlng around.
        second[0] = L.latLng(second[0].lat, second[0].lng, second[0].alt);
        this.feature._latlngs.splice(shapeIndex, 1, first, second);
        this.refresh();
        this.reset();
    }

});

var PolylineMixin = {

    getEditorClass: function (tools) {
        return (tools && tools.options.polylineEditorClass) ? tools.options.polylineEditorClass : PolylineEditor;
    },

    shapeAt: function (latlng, latlngs) {
        // We can have those cases:
        // - latlngs are just a flat array of latlngs, use this
        // - latlngs is an array of arrays of latlngs, loop over
        var shape = null;
        latlngs = latlngs || this._latlngs;
        if (!latlngs.length) return shape;
        else if (isFlat(latlngs) && this.isInLatLngs(latlng, latlngs)) shape = latlngs;
        else for (var i = 0; i < latlngs.length; i++) if (this.isInLatLngs(latlng, latlngs[i])) return latlngs[i];
        return shape;
    },

    isInLatLngs: function (l, latlngs) {
        if (!latlngs) return false;
        var i, k, len, part = [], p,
            w = this._clickTolerance();
        this._projectLatlngs(latlngs, part, this._pxBounds);
        part = part[0];
        p = this._map.latLngToLayerPoint(l);

        if (!this._pxBounds.contains(p)) { return false; }
        for (i = 1, len = part.length, k = 0; i < len; k = i++) {

            if (L.LineUtil.pointToSegmentDistance(p, part[k], part[i]) <= w) {
                return true;
            }
        }
        return false;
    }

};

if (L.Polyline) {
    L.Polyline.include(EditableMixin);
    L.Polyline.include(PolylineMixin);
    L.Polyline.addInitHook(keepEditable);
}

export default PolylineEditor;
