import L from "leaflet";
import "../../core/Base";
import DataSource from './DataSource';
import {
    Deferred,
    getJSON
} from './../../util/es6-promise';
import Version from '../../ext/Version';

class URLDataSource extends DataSource {
    constructor() {
        super();
        //多个服务器url的域名，用于解决一个域名只有6条请求管线的限制
        this.urlArray = [];
        //数据源类型
        this.type = 'URLDataSource';
        //注记数据的请求url
        this.url = null;
        //样式文件的请求接口url
        this.styleUrl = null;
        //样式文件Id
        this.styleId = 'style';
        //过滤条件
        this.filter = null;
        //纹理
        this.textures = {};
        //过滤条件字符
        this.control = null;
        //过滤的id
        this.controlId = null;
        // 不带过滤条件的url
        this.sourceUrl = null;
        //域名
        this.host = '';
        //服务名
        this.servername = '';
        this.newEngine = false;
        this.serverInfo = {};
    }



    /**
     * 加载样式文件和纹理数据
     */
    loadStyle(styleType) {
        let def0 = new Deferred();
        let def1 = new Deferred();
        let def2 = new Deferred();
        let def3 = new Deferred();

        //解析url，获取servername,styleId
        this.parseUrl();
        if (this.newEngine) {
            this.loadServerInfo();
        }
        let queryParam = this.url.split('?')[1];

        if (!this.sourceUrl) {
            this.sourceUrl = this.url + '&clientVersion=' + Version;
            this.url = this.url + '&clientVersion=' + Version;
        }

        if (this.control && this.isIE()) {
            //设置过滤条件
            getJSON({
                    type: 'post',
                    url: this.host + '/mapserver/vmap/' + this.servername + '/setControl',
                    data: 'control=' + this.control,
                    dataType: 'json'
                })
                .then(function(result) {
                    this.controlId = result.id;
                    this.url = this.sourceUrl + '&controlId=' + result.id;
                    def0.resolve();
                }.bind(this));
        } else {
            if (this.control) {
                this.url = this.sourceUrl + '&control=' + encodeURIComponent(this.control);
            } else {
                this.url = this.sourceUrl;
            }
            def0.resolve();
        }

        if (!styleType) {
            styleType = 'label';
        }

        if (this.styleId == '_default__') {
            let styleStr = 'let layers = drawer.getAllLayer(); layers.setStyle(function(level , get){' +
                ' return {"type":"_default__","_id":"1","show":true,"pointFillStyle":"#ff0000","radius":5,"lineFillStyle":"#00ff00","lineWidth":3 }' +
                '})';
            this.styleFun = new Function("drawer", "level", styleStr);
            return [def0];
        }

        //请求样式文件
        getJSON({
                url: this.host + '/mapserver/styleInfo/' + this.servername + '/' + this.styleId + '/' + styleType + '/style.js?' + Math.random() + '&' + queryParam,
                dataType: 'text'
            })
            .then(function(result) {
                if (this.newEngine) {
                    this.styleFun = new Function("render", "level", result);
                } else {
                    this.styleFun = new Function("drawer", "level", result);
                }

                def1.resolve();
            }.bind(this));

        //请求图标纹理
        getJSON({
            url: this.host + '/mapserver/styleInfo/' + this.servername + '/' + this.styleId + '/label/texture.js?' + Math.random() + '&' + queryParam,
            dataType: 'text'
        }).then(function(result) {
            let textures = JSON.parse(result);
            let totalCount = 0;
            for (let i in textures) {
                if (textures[i]) {
                    totalCount++;
                }
            }

            if (totalCount == 0) {
                def2.resolve();
                return;
            }

            let count = 0;
            for (let key in textures) {
                let img = new Image();
                img.name = key;
                img.onload = function(data) {
                    count++;
                    let name = data.target.name;
                    this.textures[name] = data.target;
                    if (count == totalCount) {
                        def2.resolve();
                    }
                }.bind(this);
                img.src = textures[key];
            }
        }.bind(this));

        this.loadFonts(queryParam, def3);
        return [def0, def1, def2, def3];
    }

    loadServerInfo() {
        if (this.host && this.servername) {
            getJSON({
                type: 'get',
                url: this.host + '/mapserver/serverInfo/' + this.servername + '.json',
                dataType: 'json'
            }).then((results) => {
                if (results && results.layerMap) {
                    for (var key in results.layerMap) {
                        this.serverInfo[key] = {
                            geometryType: results.layerMap[key].geometryType,
                            fieldsConfig: results.layerMap[key].fields
                        };
                    }
                }
            });
        }
    }

    loadFonts(queryParam, def3) {
        getJSON({
                url: this.host + '/mapserver/styleInfo/fonts/' + this.servername + '/' + this.styleId + '/list.json?' + Math.random() + '&' + queryParam,
                dataType: 'json'
            })
            .then(function(list) {
                    let loadFontRequest = [];
                    for (let i = 0; i < list.length; i++) {
                        let item = list[i];
                        loadFontRequest.push(this.loadFont(item));
                    }
                    Promise.all(loadFontRequest).then(function() {
                        def3.resolve();
                    });
                }.bind(this),
                function(err) {
                    def3.resolve();
                });
    }

    loadFont(fontParsm) {
        let def0 = new Deferred();
        let fontFaceName = fontParsm['font-family'];
        let fontFileUrl = this.getBrowserFontUrl(fontParsm['urls']);
        console.log(fontFileUrl);
        let request = new XMLHttpRequest();
        let that = this;
        request.addEventListener('readystatechange', function() {
            if (request.readyState == 4) {
                if ('fonts' in document) {
                    let junction_font = new FontFace(fontFaceName, request.response);
                    junction_font.load().then(function(loaded_face) {
                        document.fonts.add(loaded_face);
                        def0.resolve();
                    }).catch(function(error) {
                        console.log("加载字体失败" + fontFaceName + error);
                    });
                } else {
                    if ('head' in document) {
                        let base64FontHeader = "data:application/x-font-eot;charset=utf-8;base64,";
                        let codes = new Uint8Array(request.response);

                        let bin = "";
                        for (let i = 0; i < codes.byteLength; i++) {
                            bin += String.fromCharCode(codes[i]);
                        }

                        let base64String = btoa(bin);
                        let dataUrl = base64FontHeader + base64String;
                        let cssText = "@font-face{font-family:" + fontFaceName + ";src:url('" + dataUrl + "');}";
                        that.createStyleTag(cssText);
                        def0.resolve();
                    }
                }
            }
        });

        request.open('GET', fontFileUrl);
        request.responseType = 'arraybuffer';
        request.send();
        return def0;
    }

    checkFont(name) {
        let values = document.fonts.values();
        let isHave = false;
        let item = values.next();
        while (!item.done && !isHave) {
            let fontFace = item.value;
            if (fontFace.family == name) {
                isHave = true;
            }
            item = values.next();
        }
        return isHave;
    }

    createStyleTag(cssText) {
        if (cssText) {
            let head = document.head || document.getElementsByTagName('head')[0];
            let style = document.createElement('style');
            style.appendChild(document.createTextNode(cssText));
            head.appendChild(style);
        }
    }

    /**
     * 解析url
     */
    parseUrl() {
        let urlParts = this.url.split('?');
        let urlPartOne = urlParts[0].split('/mapserver/');
        this.host = urlPartOne[0];
        this.servername = urlPartOne[1].split('/')[1];
        let params = urlParts[1].split('&');
        for (let i = 0; i < params.length; i++) {
            let param = params[i];
            let keyValue = param.split('=');
            if (keyValue[0] == 'styleId') {
                this.styleId = keyValue[1];
                return;
            }
        }
    }


    /**
     * 设置过滤条件
     */
    setFilter(filter) {
        this.control = null;
        if (!this.url || !filter || (filter.layers.length == 0 && filter.order.length == 0)) {
            return;
        }

        for (let i = 0; i < filter.layers.length; i++) {
            let filterLayer = filter.layers[i];
            if (!filterLayer.id) {
                filter.layers.splice(i, 1);
            }
        }

        this.control = JSON.stringify(filter);
    }

    getTexture(key) {
        return this.textures[key];
    }

    addTexture(key, texture) {
        this.textures[key] = texture;
    }

    /**
     * 是否为ie浏览器,ie9 除外，ie9无法跨域发送post带数据的请求
     */
    isIE() {
        if (!!window.ActiveXObject || "ActiveXObject" in window) {
            //ie9 除外，ie9无法跨域发送post带数据的请求
            let b_version = navigator.appVersion
            let version = b_version.split(";");
            if (version[1]) {
                let trim_Version = version[1].replace(/[ ]/g, "");
                if (trim_Version == 'MSIE9.0') {
                    return false;
                }
            }
            return true;
        } else
            return false;
    }


    getBrowserFontUrl(urls) {
        let urlHead = this.host + '/mapserver/styleInfo/fonts/';
        let explorer = navigator.userAgent;
        let isIE = (!!window.ActiveXObject || "ActiveXObject" in window);
        //ie
        if (explorer.indexOf("MSIE") > 0 || isIE) {
            return urlHead + urls['eot'].url;
        }
        //firefox
        else if (explorer.indexOf("Firefox") > 0) {
            return urlHead + urls['woff'].url;
        }
        //Chrome
        else if (explorer.indexOf("Chrome") > 0) {
            return urlHead + urls['ttf'].url;
        }
        //Opera
        //else if (explorer.indexOf("Opera") > 0) {}
        //Safari
        //else if (explorer.indexOf("Safari") > 0) {}
        //Netscape
        //else if (explorer.indexOf("Netscape") > 0) {}
        return null;
    }
};

L.geoway.URLDataSource = URLDataSource;
export default URLDataSource;