import L from 'leaflet';
import FetchRequest from './FetchRequest';
import {WMTSCapabilities} from "../format";

export let WMTSUtil = {
    getCapabilities: function(url, fnSuccess, fnFaisule){
        var param = {
            SERVICE : "WMTS",
            VERSION : "1.0.0",
            REQUEST : "GetCapabilities"
        };
        FetchRequest.get(url, param).then(function(result){
            try{
                var doc = result.bodyText;
                var format = new WMTSCapabilities();
                var capabilities = format.read(doc);
                if (fnSuccess) {
                    fnSuccess(capabilities);
                }
            }catch(e){
                if (fnFaisule) {
                    fnFaisule(e);
                }
            }
        }).catch(function (e) {
            fnFaisule(e);
        });
    }
};

L.geoway.WMTSUtil = WMTSUtil;
L.WMTSUtil = WMTSUtil;
