import L from 'leaflet';
import UUID from '../../util/uuid'
import '../../core/Base';

class DataSource {
    constructor() {
        //数据源id
        this.id = new UUID().valueOf();
        //数据源类型
        this.type = "";
        //key为文件名，value为image对象
        this.textures = {};
    }
}
L.geoway.DataSource = DataSource;
export default DataSource;