import L from "leaflet";
import "../core/Base";

export let Geolocation = L.Class.extend({
    options: {
        enableHighAccuracy: false,
        timeout: 5000,
        maximumAge: 3000
    },
    initialize: function(options){
        L.setOptions(this, options);
    },
    /**
     * 获取当前位置信息
     * @method L.Geolocation#getCurrentPosition
     * @param {Function} callback - 回调函数
     * @param {L.Geolocation.options} options - 位置定位参数
     */
    getCurrentPosition: function(callback,options){
        L.setOptions(this, options);
        if (navigator.geolocation) {
            var locationSuccess = function(position){
                var coords = position.coords;
                var latlng = new L.LatLng(coords.latitude,coords.longitude);
                var result = {
                    latlng: latlng,
                    status: "ok"
                }
                callback(result);
            };
            var locationError = function(error){
                var errorMsg = "";
                switch(error.code){
                    case error.PERMISSION_DENIED:
                        errorMsg = "User denied the request for Geolocation.";
                        break;
                    case error.POSITION_UNAVAILABLE:
                        errorMsg = "Location information is unavailable.";
                        break;
                    case error.TIMEOUT:
                        errorMsg = "The request to get user location timed out.";
                        break;
                    case error.UNKNOWN_ERROR:
                        errorMsg = "An unknown error occurred.";
                        break;
                }
                var result = {
                    latlng: null,
                    status: "error",
                    errorMsg: errorMsg
                }
                callback(result);
            };
            navigator.geolocation.getCurrentPosition(locationSuccess, locationError,this.options);
        }else{
            var errorMsg = "Your browser does not support Geolocation!";
            var result = {
                latlng: null,
                status: "error",
                errorMsg: errorMsg
            }
            callback(result);
        }
    }
});

export let geolocation = function (options) {
    return new Geolocation(options);
}

L.geoway.geolocation = geolocation;
L.geoway.Geolocation = Geolocation;
L.Geolocation = Geolocation;
