import PathEditor from './PathEditor';
import {EditableMixin} from "./Editable";
import L from 'leaflet';

// 🍂namespace Editable; 🍂class CircleEditor
// 🍂inherits PathEditor
let CircleEditor = PathEditor.extend({

    MIN_VERTEX: 2,

    options: {
        skipMiddleMarkers: true
    },

    initialize: function (map, feature, options) {
        PathEditor.prototype.initialize.call(this, map, feature, options);
        this._resizeLatLng = this.computeResizeLatLng();
    },

    computeResizeLatLng: function () {
        // While circle is not added to the map, _radius is not set.
        var delta = (this.feature._radius || this.feature._mRadius) * Math.cos(Math.PI / 4),
            point = this.map.project(this.feature._latlng);
        return this.map.unproject([point.x + delta, point.y - delta]);
    },

    updateResizeLatLng: function () {
        this._resizeLatLng.update(this.computeResizeLatLng());
        this._resizeLatLng.__vertex.update();
    },

    getLatLngs: function () {
        return [this.feature._latlng, this._resizeLatLng];
    },

    getDefaultLatLngs: function () {
        return this.getLatLngs();
    },

    onVertexMarkerDrag: function (e) {
        if (e.vertex.getIndex() === 1) this.resize(e);
        else this.updateResizeLatLng(e);
        PathEditor.prototype.onVertexMarkerDrag.call(this, e);
    },

    resize: function (e) {
        var radius = this.feature._latlng.distanceTo(e.latlng);
        this.feature.setRadius(radius);
    },

    onDrawingMouseDown: function (e) {
        PathEditor.prototype.onDrawingMouseDown.call(this, e);
        this._resizeLatLng.update(e.latlng);
        this.feature._latlng.update(e.latlng);
        this.connect();
        // Stop dragging map.
        e.originalEvent._simulated = false;
        this.map.dragging._draggable._onUp(e.originalEvent);
        // Now transfer ongoing drag action to the radius handler.
        this._resizeLatLng.__vertex.dragging._draggable._onDown(e.originalEvent);
    },

    onDrawingMouseUp: function (e) {
        this.commitDrawing(e);
        e.originalEvent._simulated = false;
        PathEditor.prototype.onDrawingMouseUp.call(this, e);
    },

    onDrawingMouseMove: function (e) {
        e.originalEvent._simulated = false;
        PathEditor.prototype.onDrawingMouseMove.call(this, e);
    },

    onDrag: function (e) {
        PathEditor.prototype.onDrag.call(this, e);
        this.feature.dragging.updateLatLng(this._resizeLatLng);
    }

});

var CircleMixin = {

    getEditorClass: function (tools) {
        return (tools && tools.options.circleEditorClass) ? tools.options.circleEditorClass : CircleEditor;
    }

};

if (L.Circle) {
    L.Circle.include(EditableMixin);
    L.Circle.include(CircleMixin);
}

export default CircleEditor;
