import GDistance from './GDistance';
import GisTools from './../../../util/gistools/GisTools';
import AvoidUtil from './AvoidUtil';
class GCutLine {
    static cutLineFeature(feature, style) {
        let fs = [];
        let index = 0;
        if (feature.sourceData.length < 4) {
            return fs;
        }

        let cutFunctionArray = [{
                weight: feature.codeAvoidWeight,
                fun: this.createLineCodeFeatrue
            },
            {
                weight: feature.weight,
                fun: this.createLineTextFeatrue
            },
            {
                weight: feature.arrowAvoidWeight,
                fun: this.createLineArrowFeatrue
            }
        ];

        cutFunctionArray = this.sort(cutFunctionArray);


        for (let i = 0; i < cutFunctionArray.length; i++) {
            let cutFun = cutFunctionArray[i].fun;
            let f = cutFun.call(this, feature, style, index);
            index = f.index;
            if (f.feature) {
                fs.push(f.feature);
            }
        }

        // let lineText = this.createLineTextFeatrue(feature,style,index);
        // index = lineText.index;
        // if(lineText.feature){
        //     fs.push(lineText.feature);
        // }
        //
        // let lineCode= this.createLineCodeFeatrue(feature,style,index);
        // index = lineCode.index;
        // if(lineCode.feature){
        //     fs.push(lineCode.feature);
        // }
        //
        // let lineArrow= this.createLineArrowFeatrue(feature,style,index);
        // if(lineArrow.feature){
        //     fs.push(lineArrow.feature);
        // }
        return fs;
    }

    //线编码，线文字，线箭头权重排序.
    static sort(array) {
        if (array.length > 0) {
            //从大到少排序
            return array.sort(function(a, b) {
                if (a.weight < b.weight) {
                    return 1;
                } else if (a.weight == b.weight) {
                    return 0;
                } else {
                    return -1;
                }
            });
        }
    }

    /**
     * 创建线文字注记
     *  Parameters :
     *  feature
     *  index - 可用的line的index位置
     */
    static createLineTextFeatrue(feature, style, index) {
        let line = feature.sourceData;
        let d = new GDistance();
        let gaps = [];
        let textFeature = null;

        if (AvoidUtil.isNotNull(feature.label) && index < line.length) {
            //线注记的文字内容
            feature.label = feature.label + '';
            for (let count = 0; count < feature.label.length; count++) {
                gaps.push(style.lineHeight * 1.2 + 2 + style.gap);
            }

            gaps[0] = gaps[0] + 30;
            let cloneGaps = [].concat(gaps);
            let points = d.getNodePath(line, gaps);
            let textPoints = points.pointList;



            if (textPoints.length > 0) {
                index = points.index;
                if (style.isTransverse) {
                    textFeature = this.cloneFeature(feature);
                    textFeature.attributeId = textFeature.attributeId + '_text';
                    textFeature.sourceAngleData = [textPoints[0]];
                    textFeature.lineType = 'text';
                    // return {feature:textFeature,index:index};
                } else {
                    //需要延长的字个数
                    let delayLength = feature.label.length - textPoints.length;
                    if (delayLength > 0) {
                        index = line.length;
                        //摆不下的字数少于3个字延长
                        if (delayLength < style.extendedNum) {
                            this.delayTextPoint(line, textPoints, feature.label, style.chinaLabelWidth + style.gap);
                        } else {
                            return {
                                feature: null,
                                index: index
                            };
                        }
                    }

                    let p1 = [textPoints[0][0][0], textPoints[0][0][1]];
                    let p2 = textPoints[textPoints.length - 1][0];

                    //获取两点连线与x轴的夹角
                    let angle = AvoidUtil.getAngle(p1, p2);
                    textFeature = this.cloneFeature(feature);
                    textFeature.angle = angle;

                    if (style.changeDirection != false) {
                        //改变方向
                        //判断是否应该换方向
                        let showChanged = this.isChangeDirection(feature.label, p1, p2, angle);
                        if (showChanged) {
                            textPoints = textPoints.reverse();
                        }
                    }

                    textFeature.attributeId = textFeature.attributeId + '_text';
                    textFeature.sourceAngleData = textPoints;
                    textFeature.lineType = 'text';
                }
            }

            //平移
            if (style.lineOffset && textFeature) {
                if (textFeature.sourceAngleData.length == 1) {
                    textFeature.sourceAngleData[0][0][1] = textFeature.sourceAngleData[0][0][1] - style.lineOffset;
                } else {
                    textFeature.sourceAngleData = GisTools.lineOffset(textFeature.sourceAngleData, style.lineOffset);
                }

            }
        }
        return {
            feature: textFeature,
            index: index
        };
    }

    /**
     * 创建线编码注记
     *  Parameters :
     *  feature
     *  index - 可用的line的index位置
     */
    static createLineCodeFeatrue(feature, style, index) {
        let line = feature.sourceData;
        let d = new GDistance();
        let gaps = [];
        let codeFeature = null;

        let roadLabel = feature.roadCodeLabel;
        //如果有道路编码
        if (style.showRoadCode && AvoidUtil.isNotNull(roadLabel) && index < line.length) {
            let codeLine = line.slice(index, line.length - 1);
            //默认是30个像素
            gaps.push(30);
            let cPoints = d.getNodePath(codeLine, gaps);
            let codePoints = cPoints.pointList;
            if (codePoints.length == 1) {
                index = index + cPoints.index;
                codeFeature = this.cloneFeature(feature);
                codeFeature.attributeId = codeFeature.attributeId + '_code';
                codeFeature.sourceAngleData = codePoints;
                codeFeature.lineType = 'code';
                codeFeature.label = roadLabel + '';
                codeFeature.weight = feature.codeAvoidWeight;
            }
            //
            // if(codePoints.length ==0){
            //     codeFeature =  this.cloneFeature(feature);
            //     codeFeature.attributeId = codeFeature.attributeId+'_code';
            //     codeFeature.sourceAngleData = [[[line[0],line[1]],0]];
            //     codeFeature.lineType = 'code';
            //     codeFeature.label = roadLabel+'';
            //     index = index  + 2;
            //     return {feature:codeFeature,index:index};
            // }
        }
        return {
            feature: codeFeature,
            index: index
        };
    }

    /**
     * 创建线箭头注记
     *  Parameters :
     *  feature
     *  index - 可用的line的index位置
     */
    static createLineArrowFeatrue(feature, style, index) {
        let line = feature.sourceData;
        let d = new GDistance();
        let gaps = [];
        let arrowFeature = null;

        //如果有箭头
        if (style.showArrow && index < line.length) {
            let direction = style.arrowDirectionValue;
            if (style.arrowDirectionField) {
                direction = feature.attributes[style.arrowDirectionField];
            }

            style.arrowSize = style.arrowSize ? style.arrowSize : 6;
            style.arrowDistance = style.arrowDistance ? style.arrowDistance : 12;
            style.arrowLineWidth = style.arrowLineWidth ? style.arrowLineWidth : 2;
            style.arrowStrokeStyle = style.arrowStrokeStyle ? style.arrowStrokeStyle : '#666666';
            style.arrowFillStyle = style.arrowFillStyle ? style.arrowFillStyle : '#666666';
            style.arrowDirectionValue = style.arrowDirectionValue ? style.arrowDirectionValue : 0;

            let arrowLine = line.slice(index, line.length - 1);
            gaps.push(16);
            if (direction == 0) {
                gaps.push(style.arrowSize);
                gaps.push(style.arrowDistance);
            } else {
                gaps.push(style.arrowDistance);
                gaps.push(style.arrowSize);
            }

            let aPoints = d.getNodePath(arrowLine, gaps);
            let arrowPoints = aPoints.pointList;

            if (arrowPoints.length == 3) {
                arrowFeature = this.cloneFeature(feature);
                arrowFeature.attributeId = arrowFeature.attributeId + '_arrow';
                arrowFeature.sourceAngleData = arrowPoints;
                arrowFeature.lineType = 'arrow';
                arrowFeature.weight = feature.arrowAvoidWeight;
            }
        }

        return {
            feature: arrowFeature,
            index: index
        };
    }

    /**
     * 当线文字放不下时，获取延长线上的点
     *  Parameters :
     *  line - 原始线坐标
     *  textPoints - 切割之后的点坐标
     *  label - 线注记
     *  gap - 每个字之间的间隔
     *  showChanged
     *
     */
    static delayTextPoint(line, textPoints, label, gap) {
        let fristPoint = null;
        let secondPoint = null;
        //如果只能放下一个字
        if (textPoints.length == 1) {
            fristPoint = [line[0], line[1]];
        } else {
            fristPoint = textPoints[textPoints.length - 2][0];
        }
        secondPoint = textPoints[textPoints.length - 1][0];
        let angle = textPoints[textPoints.length - 1][1];

        let len = textPoints.length;
        for (let i = 1; i < label.length - len + 1; i++) {
            let p = this.getPoint(fristPoint, secondPoint, gap * i);
            let textPoint = [p, angle];
            textPoints.push(textPoint);
        }
    }

    /**
     * 克隆feature
     *  Parameters :
     *  feature - 单个线注记要素
     */
    static cloneFeature(feature) {
        return {
            type: feature.type,
            datas: feature.datas,
            centerPoint: feature.centerPoint,
            sourceData: feature.sourceData,
            label: feature.label,
            roadCodeLabel: feature.roadCodeLabel,
            attributes: feature.attributes,
            attributeId: feature.attributeId,
            styleId: feature.styleId,
            textures: feature.textures,
            xyz: feature.xyz,
            lineType: feature.lineType,
            weight: feature.weight
        };
    }

    /**
     * 是否需要改变线的方向
     *  Parameters :
     *  p1 - 线段起点
     *  p2 -线段的重点
     *  angle - 两点连线与x轴的夹角
     */
    static isChangeDirection(label, p1, p2, angle) {
        let showChange = false;
        //判断是否包含汉字
        if (/.*[\u4e00-\u9fa5]+.*$/.test(label)) {
            //如果是垂直线
            if (p1[0] == p2[0]) {
                if (p1[1] > p2[1]) {
                    showChange = true;
                    return showChange;
                }
            }

            //如果是反斜线，并且夹角与x轴的夹角大于45度
            if (angle < -45 && angle > -90) {
                if (p1[0] < p2[0]) {
                    showChange = true;
                }
            } else {
                if (p1[0] > p2[0]) {
                    showChange = true;
                }
            }
        } else {
            if (p1[0] > p2[0]) {
                showChange = true;
            }
        }
        return showChange;
    }




    /**
     * 求两点之间的距离
     */
    static getDistance(p1, p2) {
        let calX = p2[0] - p1[0];
        let calY = p2[1] - p1[1];
        return Math.pow((calX * calX + calY * calY), 0.5);
    }

    /**
     * 获取线的长度
     */
    static getLineDistance(line) {
        if (line.length < 4) {
            return 0;
        }

        let dis = 0;
        for (let i = 0; i < line.length / 2 - 1; i++) {
            let p1 = [line[2 * i], line[2 * i + 1]];
            let p2 = [line[2 * (i + 1)], line[2 * (i + 1) + 1]];
            dis = dis + this.getDistance(p1, p2);
        }
        return dis;
    }

    /**
     * 已知两点，延长距离，获取延长线上的点坐标
     */
    static getPoint(p1, p2, d) {
        let xab = p2[0] - p1[0];
        let yab = p2[1] - p1[1];
        let xd = p2[0];
        let yd = p2[1];
        if (xab == 0) {
            if (yab > 0) {
                yd = p2[1] + d;
            } else {
                yd = p2[1] - d;
            }
        } else {
            let xbd = Math.sqrt((d * d) / ((yab / xab) * (yab / xab) + 1));
            if (xab < 0) {
                xbd = -xbd
            }

            xd = p2[0] + xbd;
            yd = p2[1] + yab / xab * xbd;
        }
        return [xd, yd];
    }
}

export default GCutLine;